  bmlfChangeProjectStatus(status) {
    if (status == "NPD Approved") {
      if (!CustomValidator.bmgfNullCheckValidator(this.msNPDApprovalSetup)) {
        let parrSplitData = this.msNPDApprovalSetup.split('|')
        let parrFilterData = parrSplitData.filter(x => x == this.mobjUserData.UserId);
        if (parrFilterData.length > 0) {
          this.msChangeStatusField = status;
          this.mbStatusDialog = true;
        } else {
          this.bmlfOpenErrorBlock("SAMPLETRANS.MSG.NOTPERMISSIONFORNPDAPPROVAL");
        }
      } else {
        this.bmlfOpenErrorBlock("SAMPLETRANS.MSG.NOTPERMISSIONFORNPDAPPROVAL");
      }
    } else if (status == "Customer Accepted") {
      if (!CustomValidator.bmgfNullCheckValidator(this.msSalesApprovalSetup)) {
        let parrSplitData = this.msSalesApprovalSetup.split('|')
        let parrFilterData = parrSplitData.filter(x => x == this.mobjUserData.UserId);
        if (parrFilterData.length > 0) {
          this.msChangeStatusField = status;
          this.mbStatusDialog = true;
        } else {
          this.bmlfOpenErrorBlock("SAMPLETRANS.MSG.NOTPERMISSIONFORSALESACCEPTANCE");
        }
      } else {
        this.bmlfOpenErrorBlock("SAMPLETRANS.MSG.NOTPERMISSIONFORSALESACCEPTANCE");
      }
    } else {
      this.msChangeStatusField = status;
      this.mbStatusDialog = true;
    }
  }
  bmlfChangeStatusDlg(type) {
    if (type == "YES") {
      if (this.msChangeStatusField == "Release") {
        this.bmlfUpdateStatusData();
      } else {
        this.mbIsChangeData = true;
        this.bmlfSubmitForm("ChangeStatus");
      }
    } else {
      this.msChangeStatusField = "";
    }
    this.mbStatusDialog = false;
  }
  bmlfUpdateStatusData(): void {
    this.mbIsLoading = true;
    let pobjReqData = {
      ScreenName: "Sampling Transaction",
      LookUpKey: "UpdateStatus",
      Filter1: this.mobjCustInfoModel.SMTransactionNo,
      Filter2: this.mobjCustInfoModel.OldStatus,
      Filter3: this.mobjCustInfoModel.OldStatus
    };
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.SAVE"));
          this.bmlfGetCustomerInfoData();
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
        }
      }
    ).add(() => { this.mbIsLoading = false; })
  }
