import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';


@Injectable({
    providedIn: 'root'
})

export class SampleGroupMasterService {
    constructor(
        private httpExt: HttpCleintExtService,
        private appSettings: AppSettingsService
    ) { }
    public bmlfGetSampleGroupData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GETSampleGroupMaster`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfSaveSampleGroupData(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveSampleGroupMaster`;
            return this.httpExt.post<any>(url, headerData, null);
        }
        else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdateSampleGroupMaster`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }

    public bmlfDeleteSampleGroupData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteSampleGroupMaster`;
        return this.httpExt.post<any>(url, headerData, null);
    }
}