  constructor(private router: Router, public renderer: Renderer2,
    private lsService: LocalStorageService,
    public dateFormatService: DateFormatService,
    public toastMessageService: ToastMessageService,
    public utilService: AppUtility,
    public userFieldService: UserFieldService,
  ) {
    super(lsService, dateFormatService, userFieldService, toastMessageService, utilService);
  }

  ngOnInit(): void {
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.utilService.getBMEScreePermission("Sample Management");
    this.initGrid();
    this.getDataList();
    this.setExportData({ 'pageName': this.msScreenName });
    this.exportData.translateFrom = '';
  }
  getDataList(): void {
    this.mbLoadingEnabled = true;
    // const pobjHeaderData = { "ScreenName": this.msScreenName, "LookUpKey": "SamplingHeaderList", "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" }
    const pobjHeaderData = { "ScreenName": this.msScreenName, "LookUpKey": "NewSampleTransactionList", "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" }
    this.userFieldService.getLookupData(pobjHeaderData).subscribe(
      res => {
        let parrGridData = res.LookupData;
        parrGridData.forEach(ele => {
          ele.RecDate = new Date(ele.RecDate);
          ele.RequestedDate = new Date(ele.RequestedDate);
          ele.DeliveryDate = new Date(ele.DeliveryDate);
          let pdateDeliveryDate = ele.DeliveryDate == null ? null : new Date(ele.DeliveryDate);
          if (pdateDeliveryDate.getFullYear() == 1970) {
            ele.DeliveryDate = null;
          } else {
            ele.DeliveryDate = pdateDeliveryDate;
          }

          let pdateRequestedDate = ele.RequestedDate == null ? null : new Date(ele.RequestedDate);
          if (pdateRequestedDate.getFullYear() == 1970) {
            ele.RequestedDate = null;
          } else {
            ele.RequestedDate = pdateRequestedDate;
          }
          ele.RequestType = ele.RequestType == "N" ? "New" : "Revision";
          ele.CustomerProspect = ele.CustomerProspect == "E" ? "Customer" : (ele.CustomerProspect == "C" ? "New Customer" : (ele.CustomerProspect == "P" ? "Prospect" : "New Prospect"));
        })
        this.gridDataList = parrGridData;
        this.marrOrginalGridList = parrGridData;
        let parrSettingData = "";
        if (res.GridSettingData.length > 0) {
          parrSettingData = res.GridSettingData[0].JSONString;
        }
        this.setInitGridSettings(parrSettingData, "SAMPLETRANS");
        this.exportData.columnsConfig = this.mobjGridSettings.columnsConfig;
        this.mobjGridSettings.state.take = 50;
        this.bmlfGetFiltersCounts();
      }, error => {
        console.log(error);
      }
    ).add(() => { this.mbLoadingEnabled = false })

  }

  public bmlfResetGridSettings() {
    this.bmlfOnResetGridSettings(this.msScreenName, "SAMPLETRANS");
  }

  public bmlfSaveGridSetting(): void {
    this.bmlfOnSaveGrid(this.msScreenName);
  }
  bmlfonAdd(): void {
    //check Write permission
    if (!this.utilService.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.router.navigate(['/main/srm/sample-transaction/manage']);
  }
  bmlfonEdit(data) {
    if (!this.utilService.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.router.navigate(['/main/srm/sample-transaction/manage'], {
      queryParams: {
        TrnNo: data.TransactionNo
      }
    });
  }
