import { Component, OnInit, Renderer2, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { DateFormatService } from 'src/app/core/services/dateFormat.service'
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import * as moment from 'moment';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { SampleCustomComboValuesModel } from '../../models/sample-custom-combo-values-master.model';
import { SampleCustomComboValuesService } from '../../http/sample-custom-combo-values-service';
@Component({
  selector: 'app-sample-custom-combo-values',
  templateUrl: './sample-custom-combo-values.component.html',
  styleUrls: ['./sample-custom-combo-values.component.scss']
})
export class SampleCustomComboValuesComponent extends BaseGrid implements OnInit {
  public orginalGridList: any;
  public mbClickAddLine: boolean;
  public miGridDeleteLineIndexHeader: any;
  public itemSelected: number;
  public mbIsCloseMode: boolean = false;
  public mbSaveDialogView: boolean = false;
  public mbIsDeleteMode: boolean = false;
  public mbShowDeleteDialog: boolean = false;
  public isValid: boolean;
  public getDataList(): void { }
  public gridDataList: any = [];
  public bmlfSelectedCallback = (args) => args.dataItem;
  public msServeDateFormat: string = CommonConstants.gsServerDateFormat;
  public mbIsLoading: boolean = false;
  public msPrdKey: string;
  public msScreenName: string = 'Sampling Custom Combo Values';
  public mbChange: boolean = false;
  public mbAddDesc: boolean = false;
  public msCustomDateFormat: string = "";
  public msFormMode: string = "I";
  /**---Object Initialize-----**/
  public mobjUserData: any = {};
  public mobjModel: SampleCustomComboValuesModel = new SampleCustomComboValuesModel();
  /**---Lookup realted field----**/
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public mnumLineItemRowIndex: number = -1;
  public Dropdown: any = [];
  public marrItemGridData: any = [];
  public mbdeleteLineItem: boolean = false;
  public mbCustomControl = false;
  /**---END : Lookup realted field----**/
  /**-------START: TASK Field ----**/
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  /**-------END: TASK Field ------**/

  /**-----------START E-Signature Field----------**/
  public mbSignatureModal: boolean = false;
  public mobjSignatureData: any = {};
  public mbIsEsignature: boolean = false;
  public mbIsEsignADTPermision: boolean = false;
  public mbIsEsignAuditDialog: boolean = false;
  public mobjCommonAlertData: any = {};
  public mbCommonAlertDialog: boolean = false;
  /**-----------END E-Signature Field----------**/
  //public mskeyValue: string = ""; //FieldExtender
  @ViewChild('custom') customCtrl: any;
  @ViewChild('FieldExtender') FieldExtender: any; //FieldExtender

  constructor(public router: Router, public route: ActivatedRoute,
    public renderer: Renderer2,
    private lsService: LocalStorageService,
    public toastMessageService: ToastMessageService,
    private formService: SampleCustomComboValuesService,
    public dateFormatService: DateFormatService,
    public utilService: AppUtility,
    private lookupService: LookupService,
    public userFieldService: UserFieldService) {
    super(lsService, dateFormatService, userFieldService, toastMessageService, utilService);
  }


  ngOnInit(): void {
    /*--------- get screen permission --------*/
    this.utilService.getBMEScreePermission(this.msScreenName);
    /**--- Get default date format from service--- */
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    this.bmlfGetSampleData();
  }

  // Get Dropdown Data 
  bmlfGetSampleData() {

    let pobjReqData = {
      ScreenName: 'Custom Combo Values Master',
      LookUpKey: 'CustomComboMaster',
      Filter1: ''
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.mbCustomControl = true;
        this.msFormMode = "U";
        this.Dropdown = res.LookupData;
      }

    })
  }

  // On change DropDown
  bmlfonChangeMaster() {
    if (this.mbChange && this.mbAddDesc) {
      this.mbSaveDialogView = true;
    }

    else if (!this.mbAddDesc) {
      this.mbChange = false;
      this.Dropdown.forEach(ele => {
        if (ele.MasterName == this.mobjModel.ComboMasterName) {
          this.mobjModel.Id = ele.Id;
        }
      })
      let pobjOData = { HeaderData: [], Response: [] };
      pobjOData.HeaderData.push({
        "Id": this.mobjModel.Id,
        "ComboMasterName": this.mobjModel.ComboMasterName,
      });
      pobjOData.Response.push({
        "ResponseText": "",
        "ErrorCode": ""
      })

      let psJData = JSON.stringify(pobjOData);
      let psTempjData = JSON.stringify(psJData);
      let psJson = '{"JSON":' + psTempjData + '}';
      this.formService.bmlfGetSampleCustomComboData(psJson).subscribe(
        res => {
          let parrGridData = res.smCustomComboMasterDetails;
          this.gridDataList = parrGridData;
          this.gridDataList.forEach(ele => {
            if (ele.ComboMasterName == this.mobjModel.ComboMasterName) {
              ele.Flag = "M";
            }
          });
          //  this.gridDataList = this.bmlfSequenceGridData(this.gridDataList);
        }
      )
    }
    else {
      this.Dropdown.forEach(ele => {
        if (ele.MasterName == this.mobjModel.ComboMasterName) {
          this.mobjModel.Id = ele.Id;
        }
      })
      let pobjOData = { HeaderData: [], Response: [] };
      pobjOData.HeaderData.push({
        "Id": this.mobjModel.Id,
        "ComboMasterName": this.mobjModel.ComboMasterName,
      });
      pobjOData.Response.push({
        "ResponseText": "",
        "ErrorCode": ""
      })

      let psJData = JSON.stringify(pobjOData);
      let psTempjData = JSON.stringify(psJData);
      let psJson = '{"JSON":' + psTempjData + '}';
      this.formService.bmlfGetSampleCustomComboData(psJson).subscribe(
        res => {
          let parrGridData = res.smCustomComboMasterDetails;
          this.gridDataList = parrGridData;
          this.gridDataList.forEach(ele => {
            if (ele.ComboMasterName == this.mobjModel.ComboMasterName) {
              ele.Flag = "M";
            }
          });
          //  this.gridDataList = this.bmlfSequenceGridData(this.gridDataList);
        }
      )
    }
  }

  /** -- Add New row for defoult value --- */

  // On Change in Field
  bmlfOnFormFieldChange(rowIndex, psType) {
    this.mbChange = true;
    this.mbAddDesc = true;
    if (psType == 'marrSetupHeader') {
      if (this.gridDataList[rowIndex]['Flag'] == " " || this.gridDataList[rowIndex]['Flag'] == null) {
        this.gridDataList[rowIndex]['Flag'] = 'M';
      }
    }
  }

  // For Maintain Sequence
  bmlfSequenceGridData(gridDataList) {
    let parrTemp = gridDataList.sort((a, b) => (a.Id > b.Id) ? 1 : -1)
    for (let i = 0; i < parrTemp.length; i++) {
      parrTemp[i].Id = i + 1;
      if (parrTemp[i].Flag == "") {
        parrTemp[i].Flag = "U";
      }
    }
    // return parrTemp.sort((a, b) => (a.Sequence > b.Sequence) ? 1 : -1)
    return parrTemp;
  }

  // Validate Line Item
  bmlfValidMasterName() {
    // For check the Master Name is not blank
    if (this.mobjModel.ComboMasterName == "") {
      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('SAMPLECUSTOMCOMBOVALUE.MSG.CNAME'));
      return;
    }
    else {
      return true;
    }
  }


  // Validate Description Row Field 
  bmlfValidDesc() {

    let RowNumbers: number = 0;
    let pbResult: boolean = true;
    if (this.gridDataList.length > 0) {
      this.gridDataList.forEach((ele, i) => {
        if (pbResult == true) {
          RowNumbers = RowNumbers + 1;
          if (ele.DescValue == "" || ele.DescValue.startsWith(' ')) // For Validate Blank Value and Blank Space in Field 
          {
            CustomValidator.bmgfNullCheckValidator(ele.DescValue);
            this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('SAMPLECUSTOMCOMBOVALUE.MSG.DESC')) + " " + ele.Id);
            pbResult = false;
            document.getElementById(i + 'txtDesc').focus();
            this.isValid = false;
            return;
          }
        }
        else {
          this.isValid = true;
        }
      });
    } else {
      return true;
    }
    return pbResult;
  }

  // For Add Line Item 
  bmlfAddItemData() {


    if (this.bmlfValidMasterName()) {
      if (!this.bmlfValidDesc()) {
        return;
      }
      //this.mbChange = true;
      let pobjData: any = new SampleCustomComboValuesModel();
      let maxLineNo = 0;
      if (this.gridDataList.length == 0) {
        maxLineNo = 1;
      }
      else {
        const a = this.gridDataList.reduce(function (prev, current) {
          return (prev.Id > current.Id) ? prev : current
        })
        maxLineNo = a.Id + 1;
      }
      pobjData.Flag = "A";
      pobjData.Id = maxLineNo;
      pobjData.ComboMasterName = this.mobjModel.ComboMasterName;
      this.gridDataList.push(Object.assign({}, pobjData));
      //this.gridDataList = this.bmlfSequenceGridData(this.gridDataList);
    }
  }

  // For Delete Line Item
  bmlfonDeleteLineItem(rowIndex) {
    // Validate Permission
    if (!this.utilService.mbDelete) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };
    this.miGridDeleteLineIndexHeader = rowIndex;
    this.mbdeleteLineItem = true;
  }

  //Delete Action
  bmlfdialogDeleteLineAction(action: any) {

    if (action == 'Yes') {
      this.mbdeleteLineItem = false;
      let psFlag: string = this.gridDataList[this.miGridDeleteLineIndexHeader].Flag;
      if (psFlag == "A") {
        this.gridDataList.splice(this.miGridDeleteLineIndexHeader, 1);
        return;
      }
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            Id: this.gridDataList[this.miGridDeleteLineIndexHeader].Id,
            ComboMasterName: this.gridDataList[this.miGridDeleteLineIndexHeader].ComboMasterName,
            DescValue: this.gridDataList[this.miGridDeleteLineIndexHeader].DescValue
          }],
          Response: [{
            "ResponseText": "",
            "ErrorCode": ""
          }]
        })
      };
      this.formService.bmlfDeleteSampleCustomComboData(reqData).subscribe(res => {
        if (res == null) {
          return;
        }
        if (res.Response[0].ErrorCode != "") {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
        } else {
          this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.LINEDELETE'));
          this.gridDataList.splice(this.miGridDeleteLineIndexHeader, 1);
          //  this.gridDataList = this.bmlfSequenceGridData(this.gridDataList);
          this.bmlfonChangeMaster();
        }
      });
    }
    if (action == 'No') {
      this.mbdeleteLineItem = false;
    }
  }

  // For Saving Data 
  bmlfonSave() {

    // Validate Permission
    if (!this.utilService.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    if (!this.bmlfValidMasterName()) {
      return;
    }
    if (!this.bmlfValidDesc()) {
      return;
    }

    if (this.mbChange == false && this.FieldExtender.mbChange == false && this.customCtrl.mbChange != true && (this.msFormMode == 'U')) {
      return;
    }

    this.bmlfonSubmitData();
  }

  // On Submit Data
  bmlfonSubmitData() {

    this.mbIsLoading = true;
    let pobjJData = {
      HeaderData: this.gridDataList,
      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };
    this.formService.bmlfSaveSampleCustomComboData(pobjJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.gridDataList = res.HeaderData;
          this.msPrdKey = this.gridDataList[0].ComboMasterName;
          this.FieldExtender.mbChange = false;
          this.customCtrl.mbChange = false;
          this.mbChange = false;
          if (this.msFormMode == "I") {
            this.msFormMode = "U";
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.SAVE"));
          } else if (this.msFormMode == "U") {
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.EDIT"));
          }
          // FieldExtender -- start update
          let screenKeyValue = this.msScreenName + " — " + this.msPrdKey;
          let jsonData = this.FieldExtender.bmlfSaveData(screenKeyValue);
          if (jsonData.length > 0) {
            this.FieldExtender.bmlfCallApi(jsonData).then(res => {
              if (res != null) { }
            });
          };
          // FieldExtender -- End update
          if (this.mbIsCloseMode) {
            this.mbIsCloseMode = false;
            this.router.navigate(["/main/dashboard"]);
          }
          else {
            this.bmlfonChangeMaster();
          }
          // else {
          //   this.bmlfGetSampleData();
          // }
        }
      }
    )
  }

  // On Confirmation Modal
  bmlfOnConfirmModal(psype) {

    if (psype == 'YES') {
      this.bmlfonSave();
      this.mbSaveDialogView = false;
    } else if (psype == 'NO') {
      this.mbSaveDialogView = false;
      if (this.mbIsDeleteMode) {
        this.mbIsDeleteMode = false;
        this.mbShowDeleteDialog = true;
      }
      if (this.mbChange && this.mbAddDesc) {
        this.mbAddDesc = false;
        this.mbAddDesc = false;
        this.bmlfonChangeMaster();
      }
      else if (this.mbChange && !this.mbAddDesc) {
        this.mbChange = false;
        this.router.navigate(["/main/dashboard"]);
      }
      else {
        this.router.navigate(["/main/dashboard"]);
      }
    } else {
      this.mbAddDesc = false;
      this.mbSaveDialogView = false;
      this.mbIsCloseMode = false;
      this.bmlfonChangeMaster();
    }
  }

  // Close From
  bmlfCloseForm(): void {

    if (this.mbChange == true || this.FieldExtender.mbChange == true || this.customCtrl.mbChange == true) {
      this.mbAddDesc = false;
      this.mbSaveDialogView = true;
      this.mbIsCloseMode = true;
    } else {
      this.router.navigate(["/main/dashboard"]);

    }
  }


  //Open Task MODAL 
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = "";
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }
  //Close Task MODAL
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }
  // For PDF Help File
  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/Sampling Custom Combo Values.pdf', '_blank');
  }
}


