import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { GridDataResult, SelectionEvent } from "@progress/kendo-angular-grid";
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { SamplePurPriceApprovalModal, SamplePurPriceApprovalHdrModal } from '../../models/purchase-price-approval.model';
import { SamplePurPriceApprovalConst } from '../../constant/purchase-price-approval.const';
import { PurchasePriceApprovalService } from '../../http/purchase-price-approval.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { GroupDescriptor } from "@progress/kendo-data-query";

@Component({
  selector: 'app-purchase-price-approval',
  templateUrl: './purchase-price-approval.component.html',
  styleUrls: ['./purchase-price-approval.component.scss']
})
export class PurchasePriceApprovalComponent extends BaseGrid implements OnInit {
  @Input() msTrnKey: string = "";
  @Output() bmgfShowHeaderEvnt = new EventEmitter();

  public gridDataList = [];
  // public groups: GroupDescriptor[] = [{ field: "Sample Group" }];
  public msKeyword = '';
  public mbIsLoading: boolean = false;
  public mbLoadingEnabled = false;
  public msScreenName = "Sample Purchase Price Approval";
  public msCustomDateFormat: string = "";
  public mbIsChangeData: boolean = false;
  public gridDataListCol: any = SamplePurPriceApprovalConst.gArrLineGridColumns.map(ele => Object.assign({}, ele));
  public mobjUserData: any = {};

  public headerIssue: boolean = false;

  public marrLookupGridData: any = [];
  public mobjLookupKeyData: any = {};
  public mnumLineItemRowIndex: any = null;
  public msLookupField: string = "";
  public mobjLookupDataModel: SamplePurPriceApprovalModal = new SamplePurPriceApprovalModal();
  public msLookupTransactionNo: string = 'Transaction No';
  public msLookupVendor: string = 'Vendor';
  public mbIsLookupModal: boolean = false;

  public marrCostMethod: any = [{ key: "LAST", value: "LAST" }, { key: 'STANDARD', value: "STANDARD" }, { key: 'AVERAGE', value: "AVERAGE" }, { key: 'MARKET', value: "MARKET" }];
  public marrStatus: any = [{ key: "Entered", value: "Entered" }, { key: 'Hold', value: "Hold" }, { key: 'Approved', value: "Approved" }];
  public marrLineStatus: any = [{ key: "Entered", value: "Entered" }, { key: 'Hold', value: "Hold" }];

  public mobjModel: SamplePurPriceApprovalModal = new SamplePurPriceApprovalModal();
  public mobjHdrModel: SamplePurPriceApprovalHdrModal = new SamplePurPriceApprovalHdrModal();

  public miQtyPrecision: number = 0;
  public miQtyPrecisionFormat: string = 'n' + this.miQtyPrecision;
  public miAmountPrecision: number = 0;
  public miAmountPrecisionFormat: string = 'n' + this.miAmountPrecision;
  public miCostPrecision: number = 6;
  public miCostPrecisionFormat: string = 'n' + this.miCostPrecision;

  public filterEnabled: boolean = false;
  public msApproval: string = "Purchase Price Approval";
  public msApproved: string = "Purchase Price Approved";
  public msStatus: string = "Purchase Price Approval";

  constructor(
    private objService: PurchasePriceApprovalService,
    private lsService: LocalStorageService,
    public objToastService: ToastMessageService,
    public appUtility: AppUtility,
    private objLookupService: LookupService,
    private router: Router,
    public dateFormatService: DateFormatService,
    public userFieldService: UserFieldService,) {
    super(lsService, dateFormatService, userFieldService, objToastService, appUtility);
  }

  ngOnInit(): void {
    //this.appUtility.bmgfAddCustomLineToModel(this.gridDataListCol, this.msScreenName, 'DataTable1').then(res => { });  
    //this.appUtility.getBMEScreePermission(this.msScreenName);
    let parrDefaultFormat = JSON.parse(localStorage.getItem("defaultFormats"));
    parrDefaultFormat.forEach(ele => {
      if (ele.Default_Key == "CURR_DECIMAL") {
        this.miAmountPrecision = Number(ele.Default_Value);
        this.miAmountPrecisionFormat = 'n' + this.miAmountPrecision;
      }
    });
    let parrRoundingDefaults = JSON.parse(localStorage.getItem("RoundingDefaults"));
    this.miQtyPrecision = Number(parrRoundingDefaults[0].Default_Value);
    this.miQtyPrecisionFormat = 'n' + this.miQtyPrecision;

    this.mobjUserData = this.lsService.getUserSession();
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjHdrModel.smCostMethod = 'LAST';
    this.mobjHdrModel.HStatus = 'Entered';
    this.mobjHdrModel.smTransactionNo = this.msTrnKey;
    this.bmlfGetDefaultSetUp(); // Bug 181686
    this.getDataList();
    this.initGrid();
  }

  bmlfonselectionChange() {

  }
  // Bug 181686
  // Fetch Default Value of Costing Method
  bmlfGetDefaultSetUp() {
    this.mbIsLoading = true;
    let pobjReqData = {
      ScreenName: "Sampling Transaction",
      LookUpKey: "DefaultValues"
    };
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res != null && res.Response[0].ErrorCode == "" && res.Table1.length > 0) {
          let parrData = res.Table1[0];
          this.mobjHdrModel.smCostMethod = parrData.smDefaultValue;
        }
      }
    ).add(() => { this.mbIsLoading = false; })
  }

  getDataList() {
    let pobjReqData = {
      ScreenName: this.msScreenName,
      LookUpKey: 'GetData',
      Filter1: this.mobjHdrModel.smTransactionNo,
      Filter2: this.msStatus,
    }
    this.objLookupService.getLookup(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        let parrGridData = res.LookupData;
        //  this.allRowsSelected = false;
        parrGridData.forEach(element => {
          element.chk = false;
          if (this.msStatus != "Purchase Price Approved") {
            this.bmlfGetCostDetails(element);
          } else {
            element["Item Cost"] = this.utilService.bmgfSetPrecision(element["Item Cost"], this.miCostPrecision);
            element["Pur Price"] = this.utilService.bmgfSetPrecision(element["Pur Price"], this.miCostPrecision);
            element["Qty"] = this.utilService.bmgfSetPrecision(element["Qty"], this.miQtyPrecision);
            element["Amount"] = this.utilService.bmgfSetPrecision(element["Amount"], this.miAmountPrecision)
          }
        });
        this.gridDataList = parrGridData;

      } else {
        this.gridDataList = [];
      }
      this.headerIssue = false;
      if (res.GridSettingData.length > 0) {
        let parrSettingData = res.GridSettingData[0].JSONString;
        this.setInitGridSettings(parrSettingData, 'PURPRICEAPPROVAL');
        this.mobjGridSettings.state.take = 50;
      }
    });
  }


  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx = null) {
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    this.mnumLineItemRowIndex = pnumRowIdx;
    if (psEvent == 'blur' && (psValue == undefined || psValue == '' || this.msLookupField == psValue)) {
      this.mbIsLoading = false;
      this.msLookupField = '';
      if (psField == this.msLookupTransactionNo) {
        this.mobjHdrModel.smTransactionNo = '';
        this.gridDataList = [];
      } else if (psField == this.msLookupVendor) {
        this.gridDataList[this.mnumLineItemRowIndex]['Vendor Key'] = '';
        this.gridDataList[this.mnumLineItemRowIndex]['Vendor Name'] = '';
      }
      return false;
    }
    let psReqData: any;

    if (psField != '') {
      psReqData = { "ScreenName": psScreenName, "LookUpKey": psLookUpKey, "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    }
    this.objLookupService.getLookup(psReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        this.marrLookupGridData = res.LookupData;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.title = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;
        if (psValue) {
          this.mobjLookupKeyData.searchValue = String(psValue);
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }

        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
          let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (String(obj[pobjFirstInput]).toUpperCase() == String(psValue).toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } if (parrFilterdArr.length == 0) {
            if (psField == this.msLookupTransactionNo) {
              this.mobjHdrModel.smTransactionNo = '';
            } else if (psField == this.msLookupVendor) {
              this.gridDataList[this.mnumLineItemRowIndex]['Vendor Key'] = '';
              this.gridDataList[this.mnumLineItemRowIndex]['Vendor Name'] = '';
              this.mbIsChangeData = true;
            }
            this.mbIsLookupModal = true;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }
      } else {
        this.mbIsLoading = false;
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    });
  }

  bmlfVendorChange(rowIndex) {
    this.gridDataList[rowIndex].chk = true;
    this.mbIsChangeData = true;
  }

  bmlfSaveLookupField(pobjEvent) {
    this.mbIsLookupModal = false;
    if (pobjEvent.field == this.msLookupVendor) {
      this.gridDataList[this.mnumLineItemRowIndex]['Vendor Key'] = pobjEvent.value["Vendor_Key"];
      this.gridDataList[this.mnumLineItemRowIndex]['Vendor Name'] = pobjEvent.value["Vendor_Name"];
      this.gridDataList[this.mnumLineItemRowIndex].chk = true;
      this.mbIsChangeData = true;
    }
  }

  bmlfCloseLookupModal(pobjData) {
    this.mbIsLookupModal = pobjData.isLookupModal;
    if (pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue.toUpperCase());
      }
      if (parrFilterData.length == 0) {
        if (pobjData.field == this.msLookupTransactionNo) {
          this.mobjHdrModel.smTransactionNo = '';
        } else if (pobjData.field == this.msLookupVendor) {
          this.gridDataList[this.mnumLineItemRowIndex]['Vendor Key'] = '';
          this.gridDataList[this.mnumLineItemRowIndex]['Vendor Name'] = '';
        }
      }
    }
  }

  bmlfGetInputColumn(psField) {
    if (psField == this.msLookupTransactionNo) {
      return "SMTransactionNo";
    } else if (psField == this.msLookupVendor) {
      return "Vendor_Key";
    }
  }

  bmlfOpenErrorBlock(psMsg): void {
    this.mbIsLoading = false;
    this.objToastService.notifyError(this.appUtility.bmgfonTranslate(psMsg));
  }

  SetGridColumns(GridName, parrGridCol) {
    this.appUtility.GetJsonSetting(this.msScreenName, this.mobjUserData.UserId, GridName, parrGridCol).then((pArrGridChangedCol: any) => {
      if (pArrGridChangedCol != null && pArrGridChangedCol != undefined) {
        parrGridCol = pArrGridChangedCol
      }
    }, err => {
      console.log(err);
    });
  }

  bmlfCloseForm() {
    this.router.navigate(["/main/srm/sample-transaction"]);
  }

  bmlfSubmitForm(psType) {
    if (!this.appUtility.mbWrite) {
      this.objToastService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    if (CustomValidator.bmgfNullCheckValidator(this.mobjHdrModel.smTransactionNo)) {
      this.bmlfOpenErrorBlock("PURPRICEAPPROVAL.MSG.SELECTSAMPLENO");
      return;
    }
    this.bmlfSaveData();
  }

  bmlfSaveData(): void {
    if (this.msStatus == this.msApproved) {
      return;
    }
    let pArrGrid = this.gridDataList.filter(x => {
      if (x.chk == true) {
        return x;
      }
    })
    if (pArrGrid.length == 0) {
      this.bmlfOpenErrorBlock("COMMON.MSG.NODATASAVE");
      return;
    }
    this.mbIsLoading = true;
    let pobjJData = {
      HeaderData: pArrGrid,
      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };

    this.objService.bmlfSavePurchasePriceApprovalMaster(pobjJson, 'I').subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.objToastService.notifySuccess(this.appUtility.bmgfonTranslate('COMMON.MSG.SAVE'));
          this.mbIsChangeData = false;
          this.getDataList();
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
          this.mbIsChangeData = false;
        }
      }
    ).add(() => { this.mbIsLoading = false; })
  }

  bmlfPurPriceApprove() {
    if (this.msStatus == this.msApproved) {
      return;
    }

    if (!this.appUtility.mbWrite) {
      this.objToastService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    let pArrGrid = this.gridDataList.filter(x => {
      if (x.chk == true) {
        return x;
      }
    })

    if (pArrGrid.length < this.gridDataList.length) {
      this.bmlfOpenErrorBlock("PURPRICEAPPROVAL.MSG.SELECTALL");
      return;
    }
    let psError: string = '';
    let piRowIndex: number = 1;
    pArrGrid.forEach(ele => {
      if (psError.length == 0) {
        if (ele.Status == 'Hold') {
          psError = this.appUtility.bmgfonTranslate("PURPRICEAPPROVAL.MSG.HOLDSTATUS");
        } else if (ele['Vendor Key'] == '') {
          psError = this.appUtility.bmgfonTranslate("PURPRICEAPPROVAL.MSG.SELECTVENDOR") + piRowIndex;
        }
      }
      piRowIndex = piRowIndex + 1;
    })
    if (psError.length > 0) {
      this.objToastService.notifyError(psError);
      return;
    }
    this.mbIsLoading = true;
    let pobjJData = {
      HeaderData: pArrGrid,
      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };

    this.objService.bmlfApprovedPurchasePriceApprovalMaster(pobjJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.objToastService.notifySuccess(this.appUtility.bmgfonTranslate('PURPRICEAPPROVAL.MSG.APPROVED'));
          this.getDataList();
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
        }
      }
    ).add(() => { this.mbIsLoading = false; })
  }

  bmlfChangeCostMethod() {
    this.gridDataList.forEach(element => {
      this.bmlfGetCostDetails(element);
    });
  }

  async bmlfGetCostDetails(element) {
    let pobjReqData = {
      ScreenName: 'Sample Transaction',
      LookUpKey: 'GetSampCostPrice',
      Filter1: this.mobjHdrModel.smTransactionNo,
      Filter2: element.smRequestNo
    }
    await this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        res.LookupData.forEach(eleLookup => {
          if (element.ItemKey == eleLookup.ItemKey) {
            if (this.mobjHdrModel.smCostMethod == 'STANDARD') {
              element['Item Cost'] = eleLookup.Stdcost;
            } else if (this.mobjHdrModel.smCostMethod == 'AVERAGE') {
              element['Item Cost'] = eleLookup.Avgcost;
            } else if (this.mobjHdrModel.smCostMethod == 'LAST') {
              element['Item Cost'] = eleLookup.Lstcost;
            } else if (this.mobjHdrModel.smCostMethod == 'MARKET') {
              element['Item Cost'] = eleLookup.Marketcost;
            }
          }
        })
      }
    });
  }

  bmlfResetGridSettings() {
    this.bmlfOnResetGridSettings(this.msScreenName, "PURPRICEAPPROVAL");
  }

  bmlfOnResetGridSettings(screenName: string, localizeName: string) {
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{ "ScreenName": screenName }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.userFieldService.bmlfResetGridSetting(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate("COMMON.MSG.SERVERERROR"));
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.RESETGRIDSETTING"));
        let param = { TrnNo: this.msTrnKey };
        this.utilService.bmgfRefreshReuse('/main/srm/sample-transaction/manage', true, param);
      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText)
      }
    })
  }
  //---------------------------------------------------------------------------------------------    
  bmlfSaveGridSetting(): void {
    this.bmlfOnSaveGrid(this.msScreenName);
  }

  bmlfOnSaveGrid(screenName: string): void {
    delete this.mobjGridSettings.gridData;
    this.mobjGridSettings.filterEnabled = this.filterEnabled;
    this.mobjGridSettings.state.skip = 0; //to save always page no. 0 21/04/2020 - RD
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{ "ScreenName": screenName, "JsonString": JSON.stringify(this.mobjGridSettings) }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.userFieldService.bmlfSaveGridSetting(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate("COMMON.MSG.SERVERERROR"));
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.SAVEGRIDSETTING"));
        let param = { TrnNo: this.msTrnKey };
        this.utilService.bmgfRefreshReuse('/main/srm/sample-transaction/manage', true, param);
      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText)
      }
    })
  }

  // selectSingleRow(e, rowIndex) {
  //   this.allRowsSelected = false;
  // }

  selectAllRows(e): void {
    // if (value == true) {
    //   this.gridDataList.forEach(element => {
    //     element.chk = true;
    //   });
    // } else {
    //   this.gridDataList.forEach(element => {
    //     element.chk = false;
    //   });

    // }
    if (e.target.checked) {
      this.headerIssue = true;
      this.gridDataList.forEach((ele) => {
        ele.chk = true;
      });
    } else {
      this.headerIssue = false;
      this.gridDataList.forEach((ele) => {
        ele.chk = false;
      });
    }
  }

  bmlFilterSetting() {
    this.filterEnabled = !this.filterEnabled;
  }

  bmlfonPriceChange(rowIndex) {
    this.gridDataList[rowIndex].Amount = Number(this.gridDataList[rowIndex]['Pur Price']) * Number(this.gridDataList[rowIndex].Qty);
    this.gridDataList[rowIndex].Status = 'Entered';
    this.gridDataList[rowIndex].chk = true;
    this.mbIsChangeData = true;
  }

  bmlfonStatusChange(rowIndex) {
    this.gridDataList[rowIndex].chk = true;
    this.mbIsChangeData = true;
  }

  bmlfApprovalStatus(pbStatus) {
    if (pbStatus == false) {
      this.msStatus = this.msApproval;
    } else {
      this.msStatus = this.msApproved;
    }
    this.getDataList();
  }

}
