import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';


@Injectable({
    providedIn: 'root'
})

export class SampleCustomComboValuesService {
    constructor(
        private httpExt: HttpCleintExtService,
        private appSettings: AppSettingsService
    ) { }
    public bmlfGetSampleCustomComboData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetSampleCustomComboMasterDetails`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfSaveSampleCustomComboData(headerData: any) {
       
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveSampleCustomComboMasterDetails`;
            return this.httpExt.post<any>(url, headerData, null);
        
    }

    public bmlfDeleteSampleCustomComboData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteSampleCustomComboMasterDetails`;
        return this.httpExt.post<any>(url, headerData, null);
    }
}