import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { State } from 'src/app/reducers';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { GenericCustomFormComponent } from 'src/app/main/customization/components/generic-custom-form/generic-custom-form.component';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { SampleTransactionService } from '../../../http/sample-transaction.service';
import { CustomerInformationComponent } from '../../customer-information/customer-information.component';
//import { ExternalDocumentsComponent } from '../../external-documents/external-documents.component';
import { SampleInformationComponent } from '../../sample-information/sample-information.component';
import { CustomUserFormService } from 'src/app/main/customization/http/generic-custom-form.service';
import * as moment from 'moment';
import { LoadCustomizations } from 'src/app/main/customization/ngrx-store/customization.actions';
import { AdditionalInformationComponent } from '../../additional-information/additional-information.component';
import { LabNoteBookComponent } from '../../lab-note-book/lab-note-book.component';
//import { CommunicationComponent } from '../../communication/communication.component';
import { AlertsComponent } from 'src/app/main/dashboard/components/alerts/alerts.component';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { LabBatchComponent } from '../../lab-batch/lab-batch.component';
import { PurchasePriceApprovalComponent } from '../../purchase-price-approval/purchase-price-approval.component';

@Component({
  selector: 'app-sample-transaction-manage',
  templateUrl: './sample-transaction-manage.component.html',
  styleUrls: ['./sample-transaction-manage.component.scss']
})
export class SampleTransactionManageComponent implements OnInit {
  @ViewChild(CustomerInformationComponent) custInfoComponent: CustomerInformationComponent;
  @ViewChild(AlertsComponent) alertComponent: AlertsComponent;
  @ViewChild(SampleInformationComponent) sampleInfoComponent: SampleInformationComponent;
  @ViewChild(GenericCustomFormComponent) customFormComponent: GenericCustomFormComponent;
  @ViewChild(AdditionalInformationComponent) additionalInfoComponent: AdditionalInformationComponent;
  @ViewChild(LabNoteBookComponent) labNoteBookComponent: LabNoteBookComponent;
  @ViewChild(PurchasePriceApprovalComponent) samplepurpriceapproval: PurchasePriceApprovalComponent;
  @ViewChild(LabBatchComponent) labBatchComponent: LabBatchComponent;
  private _sidebarSize: string = '240px';
  public get sidebarSize(): string {
    return this._sidebarSize;
  }
  public set sidebarSize(newSize: string) {
    this._sidebarSize = newSize;

  }
  public msScreenName: string = "";
  public mbIsLoading: boolean = false;
  public msCustomDateFormat: string = "";
  public msMode: string = "I";
  public mbShowHeader: boolean = false;
  public msTabType: string = "";
  public mobjLabData: string = "";
  public msTrnNo: string = "";
  public msSampleReqNo: string = "";
  public msRequestNo: string = "";
  public mobjHeaderData: any = {};
  public mbSaveDialogView: boolean = false;
  public mbIsAddMode: boolean = false;
  public mbIsCopyProjectMode: boolean = false;
  public mbIsCopyRequestMode: boolean = false;
  public mbIsCloseMode: boolean = false;
  public mbIsSearchMode: boolean = false;
  public mbIsAddRequest: boolean = false;
  public msTabChange: string = "";
  public marrSampleReqList: any = [];
  public marrSampleProjectMenu: any = [];
  public marrSampleRequestMenu: any = [];

  public mbIsNotesModal: boolean = false;
  public msNotesName: string = "";
  public msNotesValue: string = "";

  public mPrimaryKeyArray: Array<any> = [];
  public msReqNoForNextNo: string = "";
  public mobjAlertData: any = {};
  public mobjMenuData: any = {};
  public mobjUserData: any = {};
  /**---Lookup realted field--- */
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public msModalTitle: string = ''
  public mnumLineItemRowIndex: number = -1;
  public msLookupField = '';

  public marrScreenPermission: any = [];
  public mbWrite: boolean = false;
  public mbDelete: boolean = false;

  public msProjectID: string = '';
  public mbShowProject: boolean = false;
  public mbIsDisabledStatusWise: boolean = false;
  public mbIsDisabledCancelledStatusWise: boolean = false;
  /**---END : Lookup realted field--- */
  constructor(private route: ActivatedRoute, public dateFormatService: DateFormatService,
    public utilService: AppUtility, private router: Router, private toastMessageService: ToastMessageService,
    public userFieldService: UserFieldService, private formService: SampleTransactionService, private customService: CustomUserFormService,
    private lsService: LocalStorageService,
    private store: Store<State>) { }

  ngOnInit(): void {
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    let psTrnNo = this.route.snapshot.queryParamMap.get('TrnNo');
    this.bmlfGetSampleMenuList();
    if (CustomValidator.bmgfNullCheckValidator(psTrnNo)) {
      this.msMode = "I";
      this.mbShowHeader = false;
      this.msTrnNo = "";
      this.msRequestNo = "Unsaved Request";
    } else {
      this.msMode = "U";
      this.mbShowHeader = true;
      this.msTrnNo = psTrnNo;
      this.bmlfGetSampleRequestList();
    }
    this.msTabType = "Customer Information";
  }

  bmlfGetSampleMenuList() {
    this.marrSampleProjectMenu = [];
    this.marrSampleRequestMenu = [];
    let pobjOData = { HeaderData: [], Response: [] };
    pobjOData.HeaderData.push({
      "UserID": this.mobjUserData.UserId,
      "Group_Type": "Sampling"
    });
    pobjOData.Response.push({
      "ResponseText": "",
      "ErrorCode": ""
    })

    let psJData = JSON.stringify(pobjOData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';
    this.formService.bmlfGetSampleMenuList(psJson).subscribe(
      res => {
        if (res != null && res.ScreenPermission.length > 0) {
          let parrScreenPermission = res.ScreenPermission;
          this.marrScreenPermission = res.ScreenPermission;
          if (res.SampleProject.length > 0) {
            let parrSampleProject = res.SampleProject;
            parrSampleProject.forEach(element => {
              let parrFilter = parrScreenPermission.filter(x => x.MenuItem_ID == element.MenuItem_ID);
              if (parrFilter.length > 0) {
                element.Permission = parrFilter[0].Permission;
                element.permission1 = parrFilter[0].permission1;
                this.marrSampleProjectMenu.push(element);
              }
            });
            this.msScreenName = this.marrSampleProjectMenu[0].MenuItem_Name;
            if (this.marrSampleProjectMenu[0].RoutingPath.includes("customization")) {
              this.msTabType = "customForm";
            }
            this.getBMEScreePermission(this.msScreenName);
          }
          if (res.SampleRequest.length > 0) {
            let parrSampleRequest = res.SampleRequest;
            parrSampleRequest.forEach(element => {
              let parrFilter = parrScreenPermission.filter(x => x.MenuItem_ID == element.MenuItem_ID);
              if (parrFilter.length > 0) {
                element.Permission = parrFilter[0].Permission;
                element.permission1 = parrFilter[0].permission1;
                this.marrSampleRequestMenu.push(element);
              }
            });
          }
        }
      }
    ).add(() => {
    })
  }

  /**---------Get sample request no ---------- */
  bmlfGetSampleRequestList(): void {
    this.marrSampleReqList = [];
    let pobjReqData = {
      ScreenName: "Sampling Transaction",
      LookUpKey: "GetAllSampleDetails",
      Filter1: this.msTrnNo
    };
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(
      res => {
        if (res.Response[0].ErrorCode == "") {
          if (res.LookupData.length > 0) {
            this.marrSampleReqList = res.LookupData;
            this.msRequestNo = this.marrSampleReqList[0].SMRequestNo;
          } else {
            this.msRequestNo = "Unsaved Request";
          }
        } else {
          this.msRequestNo = "Unsaved Request";
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
        }
      }
    ).add(() => { })
  }
  bmlfonSwitchTab(value, data) {
    if (CustomValidator.bmgfNullCheckValidator(this.msTrnNo)) {
      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('SAMPLETRANS.MSG.FIRSTLYSAVEDATA'));
    } else {
      if (data.RoutingPath.includes("customization")) {
        value = "customForm";
      }
      if (this.msTabType != value || (this.msTabType == value && this.msScreenName != data.MenuItem_Name)) {
        let pbChangeData: boolean = false;
        pbChangeData = this.bmlfCheckMbChange();
        if (pbChangeData) {
          this.mbSaveDialogView = true;
          this.msTabChange = value;
          this.mobjMenuData = data
        } else {
          this.bmlfSetTabData(value, data);
          this.msScreenName = data.MenuItem_Name;
          this.getBMEScreePermission(this.msScreenName);
        }
      }

    }
  }
  bmlfSetTabData(value, data) {
    if (value == "Sample Information") {
      this.bmlfSetTabForSampleInfo(this.msRequestNo == "Unsaved Request" ? "" : this.msRequestNo);
    } else if (value == "customForm") {
      localStorage.removeItem('Custom');
      this.bmlfGetCustomFormData(data.MenuItem_Name, value);
      this.msScreenName = this.mobjMenuData.MenuItem_Name;
    } else if (value == "Communication") {
      this.mobjAlertData.ScreenName = "Sample Transaction";
      this.mobjAlertData.PrimaryKey = this.mobjHeaderData.SMPROJECTID;
      this.msTabType = value;
      this.msScreenName = this.mobjMenuData.MenuItem_Name;
    } else {
      this.msTabType = value;
      this.msScreenName = this.mobjMenuData.MenuItem_Name;
    }

  }
  bmlfInsertRecord() {
    let pbChangeData: boolean = false;
    pbChangeData = this.bmlfCheckMbChange();
    if (pbChangeData) {
      this.mbSaveDialogView = true;
      this.mbIsAddMode = true;
    } else {
      this.bmlfRefreshForm();
    }
  }
  bmlfUpdateHeader() {
    this.msMode = "U";
    this.mbShowHeader = false;
  }
  bmlfSearchRecord() {
    let pbChangeData: boolean = false;
    pbChangeData = this.bmlfCheckMbChange();
    if (pbChangeData) {
      this.mbSaveDialogView = true;
      this.mbIsSearchMode = true;
    } else {
      this.bmlfOpenLookupModal("SearchProject", "Sampling Header", "SamplingHeaderList", "click", "", "custom", "cmdSearch", 'SAMPLETRANS.TITLE');
    }
  }
  bmlfChangeRequestNo(psReqNo) {
    this.msRequestNo = psReqNo;
    this.bmlfAddSampleInfo(psReqNo);
  }
  bmlfAddSampleInfo(psReqNo) {
    if (CustomValidator.bmgfNullCheckValidator(this.msTrnNo)) {
      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('SAMPLETRANS.MSG.FIRSTLYSAVEDATA'));
    } else {
      //this.msTabType = "";
      let pbChangeData: boolean = false;
      pbChangeData = this.bmlfCheckMbChange();
      if (pbChangeData) {
        this.mbSaveDialogView = true;
        if (psReqNo == "") {
          this.mbIsAddRequest = true;
        } else {
          this.msSampleReqNo = psReqNo;
          this.msTabChange = "Sample Information";
        }
      } else {
        this.msTabType = "";
        this.bmlfSetTabForSampleInfo(psReqNo);
      }
    }
  }

  bmlfSetTabForSampleInfo(psReqNo) {
    this.msTabType = "";
    if (psReqNo == "") {
      this.msRequestNo = "Unsaved Request";
      this.msScreenName = "Sample Information";
      this.msMode = "I";
      this.msSampleReqNo = "";
      if (this.marrSampleReqList.length > 0) {
        this.msReqNoForNextNo = this.marrSampleReqList[this.marrSampleReqList.length - 1].SMRequestNo;
      } else {
        this.msReqNoForNextNo = "";
      }
      setTimeout(() => {
        this.msTabType = "Sample Information Add"
      }, 500);
    } else {
      this.msReqNoForNextNo = "";
      this.msMode = "U";
      this.msSampleReqNo = psReqNo;

      setTimeout(() => {
        this.msTabType = "Sample Information"
      }, 500);
    }
    this.msScreenName = "Sample Information";
  }

  bmlfSaveSampleTranData(psType = "") {
    if (!this.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    if (this.custInfoComponent != undefined) {
      this.custInfoComponent.bmlfSubmitForm(psType);
    }
    if (this.sampleInfoComponent != undefined) {
      this.sampleInfoComponent.bmlfSubmitForm(psType);
    }
    if (this.customFormComponent != undefined) {
      this.customFormComponent.bmlfonSaveMainFunction(psType);
    }
    if (this.additionalInfoComponent != undefined) {
      this.additionalInfoComponent.bmlfSubmitForm(psType);
    }
    if (this.labNoteBookComponent != undefined) {
      this.labNoteBookComponent.bmlfSubmitForm(psType);
    }
    if (this.labBatchComponent != undefined) {
      this.labBatchComponent.bmlfSubmitForm(psType);
    }
    if (this.samplepurpriceapproval != undefined) {
      this.samplepurpriceapproval.bmlfSubmitForm(psType);
    }
  }

  bmlfCloseSampleTranData() {
    let pbChangeData: boolean = false;
    pbChangeData = this.bmlfCheckMbChange();
    if (pbChangeData) {
      this.mbSaveDialogView = true;
      this.mbIsCloseMode = true;
    } else {
      if (this.custInfoComponent != undefined) {
        this.custInfoComponent.bmlfCloseForm();
      }
      if (this.alertComponent != undefined) {
        this.router.navigate(["/main/srm/sample-transaction"]);
      }
      if (this.sampleInfoComponent != undefined) {
        this.sampleInfoComponent.bmlfCloseForm();
      }
      if (this.customFormComponent != undefined) {
        this.router.navigate(["/main/srm/sample-transaction"]);
      }
      if (this.additionalInfoComponent != undefined) {
        this.additionalInfoComponent.bmlfCloseForm();
      }
      if (this.labNoteBookComponent != undefined) {
        this.labNoteBookComponent.bmlfCloseForm();
      }
      if (this.labBatchComponent != undefined) {
        this.labBatchComponent.bmlfCloseForm();
      }
      if (this.samplepurpriceapproval != undefined) {
        this.samplepurpriceapproval.bmlfCloseForm();
      }
    }
  }
  bmlfCheckMbChange() {
    if (this.mbIsDisabledStatusWise || this.mbIsDisabledCancelledStatusWise) {
      return false;
    }
    if (this.custInfoComponent != undefined) {
      if (this.custInfoComponent.mbIsChangeData || this.custInfoComponent.FieldExtender.mbChange || this.custInfoComponent.customCtrl.mbChange == true) {
        return true;
      } else {
        return false;
      }
      // return this.custInfoComponent.mbIsChangeData;
    }
    if (this.labNoteBookComponent != undefined) {
      return this.labNoteBookComponent.mbIsChangeData
    }
    if (this.sampleInfoComponent != undefined) {
      if (this.sampleInfoComponent.mbIsChangeData || this.sampleInfoComponent.FieldExtender.mbChange || this.sampleInfoComponent.customCtrl.mbChange == true) {
        return true;
      } else {
        return false;
      }
      // return this.sampleInfoComponent.mbIsChangeData;
    }
    if (this.customFormComponent != undefined) {
      return this.customFormComponent.mbChange;
    }
    if (this.additionalInfoComponent != undefined) {
      return this.additionalInfoComponent.mbIsChangeData;
    }
    if (this.labBatchComponent != undefined) {
      return this.labBatchComponent.mbIsChangeData
    }
    if (this.samplepurpriceapproval != undefined) {
      return this.samplepurpriceapproval.mbIsChangeData
    }
  }
  bmlfSetMbChange() {
    if (this.custInfoComponent != undefined) {
      this.custInfoComponent.mbIsChangeData = false;
    }
  }
  bmlfOnConfirmModal(psype) {
    if (psype == 'YES') {
      if (this.mbIsAddMode) {
        this.bmlfSaveSampleTranData("New");
      } else if (this.mbIsSearchMode) {
        this.bmlfSaveSampleTranData("Search");
      } else if (this.mbIsAddRequest) {
        this.bmlfSaveSampleTranData("AddSample");
      } else if (this.msTabChange != "") {
        this.bmlfSaveSampleTranData("TabChange");
      } else if (this.mbIsCloseMode) {
        this.bmlfSaveSampleTranData("Close");
      } else if (this.mbIsCopyProjectMode) {
        this.bmlfSaveSampleTranData("CopyProject");
      } else if (this.mbIsCopyRequestMode) {
        this.bmlfSaveSampleTranData("CopyRequest");
      }
      this.mbSaveDialogView = false;
    } else if (psype == 'NO') {
      this.mbSaveDialogView = false;
      if (this.mbIsAddMode) {
        this.mbIsAddMode = false;
        this.bmlfRefreshForm();
      } else if (this.mbIsSearchMode) {
        this.mbIsSearchMode = false;
        this.bmlfOpenLookupModal("SearchProject", "Sampling Header", "SamplingHeaderList", "click", "", "custom", "cmdSearch", 'SAMPLETRANS.TITLE');
      } else if (this.mbIsAddRequest) {
        this.mbIsAddRequest = false;
        this.msTabType = "";
        this.bmlfSetTabForSampleInfo("");
        // this.msRequestNo = "Unsaved Request";
        // this.msScreenName = "Sample Information";
        // this.msMode = "I";
        // this.msSampleReqNo = "";
        // if (this.marrSampleReqList.length > 0) {
        //   this.msReqNoForNextNo = this.marrSampleReqList[this.marrSampleReqList.length - 1].SMRequestNo;
        // } else {
        //   this.msReqNoForNextNo = "";
        // }
        // setTimeout(() => {
        //   this.msTabType = "Sample Information Add"
        // }, 500);
        
      } else if (this.msTabChange != "") {
        this.bmlfSetTabData(this.msTabChange, this.mobjMenuData);
        //this.msTabType = this.msTabChange;
        this.msTabChange = "";
        this.mobjMenuData = {};
      } else if (this.mbIsCopyProjectMode == true) {
        this.mbIsCopyProjectMode = false;
        this.mbShowProject = true;
      } else if (this.mbIsCopyRequestMode == true) {
        this.mbIsCopyRequestMode = false;
        this.bmlfCopySampleRequestData();
      } else {
        this.mbIsCloseMode = false;
        if (this.mbShowHeader == false && this.msMode == "U") {
          this.mbShowHeader = true;
          this.bmlfSetMbChange();
        } else {
          this.router.navigate(["/main/srm/sample-transaction"]);
        }
      }
    } else {
      this.mbSaveDialogView = false;
      this.mbIsAddMode = false;
      this.mbIsCloseMode = false;
      this.msTabChange = "";
      this.mbIsSearchMode = false;
      this.mbIsAddRequest = false;
      this.mbIsCopyProjectMode = false;
      this.mbIsCopyRequestMode = false;
    }
  }
  bmlfRefreshForm() {
    this.utilService.bmgfRefreshReuse('/main/srm/sample-transaction/manage', false);
  }
  bmlfShowHeaderEvnt(event) {
    if (event.type == "Close") {
      this.mbShowHeader = true;
    } else if (event.type == "Get") {
      this.mobjHeaderData = event.data;
      if (this.mobjHeaderData.Status == 'Hold' || this.mobjHeaderData.Status == 'Cancelled' || this.mobjHeaderData.Status == 'Closed') {
        this.mbIsDisabledStatusWise = true;
        if (this.mobjHeaderData.Status == 'Cancelled' || this.mobjHeaderData.Status == 'Closed') {
          this.mbIsDisabledCancelledStatusWise = true
        } else {
          this.mbIsDisabledCancelledStatusWise = false;
        }
      } else {
        this.mbIsDisabledStatusWise = false;
      }
      if (this.custInfoComponent.marrPriority.length > 0) {
        let parrFilterPriority = this.custInfoComponent.marrPriority.filter(x => x.Id == this.mobjHeaderData.smPriority);
        if (parrFilterPriority.length > 0) {
          this.mobjHeaderData.smPriorityName = parrFilterPriority[0].DescValue;
        }
      }
    } else if (event.type == "Save") {
      this.mobjHeaderData = event.data;
      this.msMode = "U";
      this.mbShowHeader = true;
      this.msTrnNo = event.key;
      if (this.mobjHeaderData.Status == 'Hold' || this.mobjHeaderData.Status == 'Cancelled' || this.mobjHeaderData.Status == 'Closed') {
        this.mbIsDisabledStatusWise = true;
        if (this.mobjHeaderData.Status == 'Cancelled' || this.mobjHeaderData.Status == 'Closed') {
          this.mbIsDisabledCancelledStatusWise = true
        } else {
          this.mbIsDisabledCancelledStatusWise = false;
        }
      } else {
        this.mbIsDisabledStatusWise = false;
      }
      if (this.custInfoComponent.marrPriority.length > 0) {
        let parrFilterPriority = this.custInfoComponent.marrPriority.filter(x => x.Id == this.mobjHeaderData.smPriority);
        if (parrFilterPriority.length > 0) {
          this.mobjHeaderData.smPriorityName = parrFilterPriority[0].DescValue;
        }
      }
    } else if (event.type == "New") {
      this.bmlfRefreshForm();
    } else if (event.type == "Search") {
      this.bmlfOpenLookupModal("SearchProject", "Sampling Header", "SamplingHeaderList", "click", "", "custom", "cmdSearch", 'SAMPLETRANS.TITLE');
    } else if (event.type == "TabChange") {
      this.bmlfSetTabData(this.msTabChange, this.mobjMenuData);
      this.msTabChange = "";
      this.mobjMenuData = {};
    } else if (event.type == "GetRequestNo") {
      this.marrSampleReqList.push({ "SMRequestNo": event.ReqNo, "smSampleDescription": event.SampleDescription })
      this.msRequestNo = event.ReqNo;
      this.msMode = "U";
    } else if (event.type == "AddSample") {
      this.msRequestNo = "Unsaved Request";
      this.msScreenName = "Sample Information";
      this.msMode = "I";
      this.msSampleReqNo = "";
      if (this.marrSampleReqList.length > 0) {
        this.msReqNoForNextNo = this.marrSampleReqList[this.marrSampleReqList.length - 1].SMRequestNo;
      } else {
        this.msReqNoForNextNo = "";
      }
      setTimeout(() => {
        this.msTabType = "Sample Information Add"
      }, 500);
    } else if (event.type == "CopyProject") {
      this.mbShowProject = true;
    } else if (event.type == "CopyRequest") {
      this.bmlfCopySampleRequestData();
    }
  }

  bmlfUpdateCustomerData() {
    this.custInfoComponent.bmlfOpenUpdateModal();
  }

  // Transfer Formula
  bmlfTransferFromula() {
    this.labNoteBookComponent.bmlfTransFormula();
  }
  // Fetch Bom item
  // bmlfFetchBomItem() {
  //   this.labNoteBookComponent.bmlfGetBomDetailData();
  // }
  bmlfPurPriceApprove() {
    if (!this.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    let pbChangeData: boolean = false;
    pbChangeData = this.bmlfCheckMbChange();
    if (pbChangeData) {
      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate("COMMON.MSG.SAVEFIRST"));
      return;
    }
    this.samplepurpriceapproval.bmlfPurPriceApprove();
  }

  /** ----START : NOTES FUNCTIONALITY--- */
  bmlfOpenNotesModal(psName: string, psValue: string) {
    if (!CustomValidator.bmgfNullCheckValidator(psValue)) {
      this.mbIsNotesModal = true;
      this.msNotesName = psName;
      this.msNotesValue = psValue;
    } else {
      //Key cannot be empty
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.NOTES'));
    }
  }
  bmlfCloseNotesModal(pobjEvent: any): void {
    this.mbIsNotesModal = pobjEvent.option;
  }
  /** ----END : NOTES FUNCTIONALITY--- */

  /**--------------Get Custom Screen data----------------- */
  bmlfGetCustomFormData(psScreenName, value) {
    this.mbIsLoading = true;
    this.msTabType = "";
    this.customService.getCustomFormData(psScreenName, '').subscribe(res => {
      if (res != null) {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == '') {
          let psDbTableName = res.Response[0]['DbTableName'];
          if (res.HeaderSchema.length > 0) {
            this.mPrimaryKeyArray = [];
            res.HeaderSchema.forEach(data => {
              Object.keys(data).forEach(key => {
                if (key == 'PrimaryKey' && data[key] == true) {
                  this.mPrimaryKeyArray.push(data['ColumnName'])
                }
              })
            })
            let psParamJson = {};
            let psParamArray = [];
            for (var i = 0; i < this.mPrimaryKeyArray.length; i++) {
              if (this.mPrimaryKeyArray[i] == "SMTransactionNo") {
                psParamJson[this.mPrimaryKeyArray[i]] = this.mobjHeaderData.SMTransactionNo;
              } else if (this.mPrimaryKeyArray[i] == "SMRequestNo") {
                psParamJson[this.mPrimaryKeyArray[i]] = this.msRequestNo;
              }
            }
            psParamArray.push(psParamJson);
            localStorage.setItem('Custom', JSON.stringify({ 'parentscreenName': psScreenName, 'tableName': psDbTableName, 'paramArray': psParamArray }));
            this.store.dispatch(new LoadCustomizations(res));
            this.msTabType = value;
          }
        }
      }
      else {
        this.mbIsLoading = false;
      }
    });
  }


  //Open LOOKUP MODAL
  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, psTitle, piRowIdx = null) {
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    if (psTitle != "") {
      this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psTitle);
    } else {
      this.mobjLookupKeyData.title = psTitle;
    }
    let psLookup = psLookUpKey;

    let pobjReqData: any = {
      ScreenName: psScreenName,
      LookUpKey: psLookup,
      Filter1: "",
      Filter2: "",
      Filter3: "",
      Filter4: "",
      Filter5: ""
    }
    this.utilService.bmgfOnKeyInputDisable();
    this.mbIsLoading = true;
    this.userFieldService.getLookupData(pobjReqData).subscribe(res => {
      this.utilService.bmgfOnKeyInputEnable();
      this.mbIsLoading = false;
      if (res == null) {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.NODATA'));
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        res.LookupData.forEach((ele) => {
          Object.keys(ele).forEach((key) => {
            let psKey = key.toUpperCase();
            if (psKey.search('DATE') != -1) {
              if (ele[key] && ele[key] != '') {
                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
              } else {
                ele[key] = '';
              }
            }
          });
          ele.RequestType = ele.RequestType == "N" ? "New" : "Revision";
          ele.CustomerProspect = ele.CustomerProspect == "E" ? "Customer" : (ele.CustomerProspect == "C" ? "New Customer" : (ele.CustomerProspect == "P" ? "Prospect" : "New Prospect"));
        })
        this.marrLookupGridData = res.LookupData;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;

        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue.toString();
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        this.mbIsLookupModal = true;
      } else {
        this.mbIsLookupModal = false;
        this.mbIsLoading = false;
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.NODATA'));
      }
    }, err => {
      this.mbIsLoading = false;
      console.log(err);
    });
  }

  //Close LOOKUP MODAL
  bmlfCloseLookupModal(pobjData) {
    this.mbIsLookupModal = pobjData.isLookupModal;
  }

  bmlfSaveLookupField(pobjData) {
    this.mbIsLookupModal = false;
    this.mbIsLoading = false;
    let param = { TrnNo: pobjData.value["TransactionNo"] };
    this.utilService.bmgfRefreshReuse('/main/srm/sample-transaction/manage', true, param);
  }

  bmlfCopySampleTranData() {
    if (this.msTrnNo == "") {
      return;
    }
    let pbChangeData: boolean = false;
    pbChangeData = this.bmlfCheckMbChange();
    if (pbChangeData) {
      this.mbSaveDialogView = true;
      this.mbIsCopyProjectMode = true;
    } else {
      this.mbShowProject = true;
    }
  }

  bmlfCopySampleInfo() {
    if (this.msTrnNo == "" || this.msRequestNo == "" || this.msRequestNo == "Unsaved Request") {
      return;
    }
    let pbChangeData: boolean = false;
    pbChangeData = this.bmlfCheckMbChange();
    if (pbChangeData) {
      this.mbSaveDialogView = true;
      this.mbIsCopyRequestMode = true;
    } else {
      this.bmlfCopySampleRequestData();
    }
  }
  bmlfCopySampleRequestData() {
    let pobjJData = {
      ParameterData: [{ "Mode": "Insert", "CopyMode": "COPY" }],
      GetSamplingInfoData: [{ "SMTransactionNo": this.msTrnNo, "SMRequestNo": this.msRequestNo }],

      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };
    this.mbIsLoading = true;
    this.formService.bmlfSaveSamplingDetailData(pobjJson).subscribe(
      res => {
        if (res.Response[0].ErrorCode == "") {
          this.toastMessageService.notifySuccess(res.Response[0].ResponseText);
          this.bmlfGetSampleRequestList();
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
        };
      }
    ).add(() => { this.mbIsLoading = false; })
  }

  bmlfProjectID(type) {
    if (type == 'close') {
      this.mbShowProject = false;
    } else if (this.msProjectID == '') {
      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('SAMPLETRANS.MSG.PROJECTIDREQ'));
    } else {
      this.mbShowProject = false;
      this.custInfoComponent.bmlfCopySampleTranData(this.msProjectID);
    }

  }

  bmlfGenerateRevision() {
    if (this.msTrnNo == "") {
      return;
    }
    let pobjJData = {
      ParameterData: [{ "Mode": "Automatic", "SMTransactionNoOld": this.msTrnNo, "SMPROJECTID": this.mobjHeaderData.SMPROJECTID, "SMTransactionNo": "" }],
      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };

    this.formService.bmlfRevisionSamplingDataSave(pobjJson).subscribe(
      res => {
        // this.router.navigate(['/main/srm/sample-transaction/manage'], {
        //   queryParams: {
        //     TrnNo: res.ParameterData[0].SMTransactionNo
        //   }
        // });
        this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.REVISESUCESS") + " - " + res.ParameterData[0].SMTransactionNo);
        if (res.ParameterData.length > 0) {
          let param = { TrnNo: res.ParameterData[0].SMTransactionNo };
          this.utilService.bmgfRefreshReuse('/main/srm/sample-transaction/manage', true, param);
        }
      });

  }

  getBMEScreePermission(psScreenName: string, pnMenuItem_ID?: number) {
    let objScreenPermission = this.marrScreenPermission;
    let objPermission: any;
    if (typeof pnMenuItem_ID != 'undefined') {
      objPermission = objScreenPermission.filter(x => {
        if (x.MenuItem_ID == pnMenuItem_ID) {
          return x;
        }
      })
    } else {
      objPermission = objScreenPermission.filter(x => {
        if (x.MenuItem_Name == psScreenName) {
          return x;
        }
      })
    }

    if (objPermission.length > 0) {
      this.mbWrite = false;
      this.mbDelete = false;

      if (objPermission[0].Permission == "Write") {
        this.mbWrite = true;
      }
      if (objPermission[0].permission1 == "Delete") {
        this.mbDelete = true;
      }
    }
    else {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      this.router.navigate(["/main/srm/sample-transaction"]);
    }
  }
  bmlfChangeHeaderStatus(status) {
    this.custInfoComponent.bmlfChangeProjectStatus(status);
  }
  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/Sample Management.pdf', '_blank');
  }
  bmlfPrintReport() {
    this.mbIsLoading = true;
    let parrDataList = [{
      ReportName: "R&D Sample Request Report",
      TransactionNo: this.mobjHeaderData.SMTransactionNo,
      RequestNo: this.msRequestNo,
      ListingFlag: false,
      ListingVariable: "",
      Parameter: "",
    }]
    let pobjJData = {
      DistReport: parrDataList,
      Response: [{
        "ResponseText": "",
        "ErrorCode": "",
        "Pdfpath": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };
    this.userFieldService.bmlfDISTReportProcess(pobjJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res == null) {
          this.toastMessageService.notifyError(this.utilService.bmgfonTranslate("COMMON.MSG.SERVERERROR"));
        }
        if (res.Response[0].ErrorCode == "") {
          let pDefaultReportType = localStorage.getItem('reportType');
          let pPDFSrc = res.Response[0].Pdfpath;
          if (pDefaultReportType == 'Print') {
            this.utilService.bmgfPrintPDF(pPDFSrc);
          } else if (pDefaultReportType == 'NewTab') {
            this.utilService.bmgfOpenPDFNewTab(pPDFSrc);
          } else {
            this.utilService.bmgfDownloadFile(pPDFSrc, "Sample Request Report", pDefaultReportType);
          }
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
        }
      }
    ).add(() => { this.mbIsLoading = false; })
  }
}
