import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { SampleTransactionService } from '../../http/sample-transaction.service';
import { SampleInfoModel } from '../../models/sample-transaction.model';
import * as moment from 'moment';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-sample-information',
  templateUrl: './sample-information.component.html',
  styleUrls: ['./sample-information.component.scss']
})
export class SampleInformationComponent implements OnInit {
  @Input() msMode: string = "I";
  @Input() msTrnKey: string = "";
  @Input() msReqNo: string = "";
  @Input() msReqNoForNextNo: string = "";
  @Output() bmgfShowHeaderEvnt = new EventEmitter();
  public msScreenName = "Sample Information";
  public autoCorrect: boolean = true;
  public mbIsLoading: boolean = false;
  public mbIsChangeData: boolean = false;
  public mobjSampleInfoModel: SampleInfoModel = new SampleInfoModel();
  public marrStatus: any = [];
  public msServeDateFormat = CommonConstants.gsServerDateFormat;
  public msCustomDateFormat: string = "";
  public miPricePrecision: number = 6;
  public miPricePrecisionFormat: string = 'n' + this.miPricePrecision;
  public miQtyPrecision: number = 0;
  public miQtyPrecisionFormat: string = 'n' + this.miQtyPrecision;
  /**---Lookup realted field--- */
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public msModalTitle: string = ''
  public mnumLineItemRowIndex: number = -1;
  public msLookupField = '';
  /**---END : Lookup realted field--- */
  /**-------Start--Comment Description -----*/
  public mbCommentDialog: boolean = false;
  public CommentDescription: string = "";
  public msCommentMaxlength: number = 5000;
  public msCommentField: string = "";
  public msHomeCurrency: string = '';
  public mskeyValue: string = ""; //FieldExtender
  public mbCustomControl = true;
  @ViewChild('custom') customCtrl: any;
  @ViewChild('FieldExtender') FieldExtender: any; //FieldExtender

  /**-------END--Comment Description -----*/
  constructor(private dateFormatService: DateFormatService,
    private toastMessageService: ToastMessageService, private formService: SampleTransactionService,
    public utilService: AppUtility, private lookupService: LookupService,
    public userFieldService: UserFieldService, private router: Router) { }

  ngOnInit(): void {
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    let parrRoundingDefaults = JSON.parse(localStorage.getItem("RoundingDefaults"));
    this.miQtyPrecision = Number(parrRoundingDefaults[0].Default_Value);
    this.miQtyPrecisionFormat = 'n' + this.miQtyPrecision;
    this.bmlfGetStatusData();
    this.bmlfGetDefault();
    if (!CustomValidator.bmgfNullCheckValidator(this.msTrnKey)) {
      if (CustomValidator.bmgfNullCheckValidator(this.msReqNo)) {
        this.bmlfGetRequestNo();
      } else {
        this.mskeyValue = this.msTrnKey + this.msReqNo;
        this.bmlfGetSampleInfoData();
      }
    }
  }
  bmlfGetStatusData(): void {
    this.mbIsLoading = true;
    let pobjReqData = {
      ScreenName: "Sampling Transaction",
      LookUpKey: "GetAllStatus",
    };
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          if (res.LookupData.length > 0) {
            this.marrStatus = res.LookupData;
           // this.mobjSampleInfoModel.smStatus = "NEW";
          }
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
        }
      }
    ).add(() => { this.mbIsLoading = false; })
  }
  bmlfGetRequestNo() {
    this.mbIsLoading = true;
    let pobjOData = { GetNextRequestNo: [], Response: [] };
    pobjOData.GetNextRequestNo.push({
      "SMTransactionNo": this.msTrnKey,
      "SMRequestNo": this.msReqNoForNextNo,
      "SMNextRequestNo": ""
    });
    pobjOData.Response.push({
      "ResponseText": "",
      "ErrorCode": ""
    })

    let psJData = JSON.stringify(pobjOData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';
    this.formService.bmlfGetNextRequestNo(psJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res != null && res.GetNextRequestNo.length > 0) {
          this.mobjSampleInfoModel.SMRequestNo = res.GetNextRequestNo[0].SMNextRequestNo;
          this.mobjSampleInfoModel.smStatus = "NEW";
        }
      }
    ).add(() => {
      this.mbIsLoading = false;
    })
  }
  bmlfGetSampleInfoData() {
    this.mbIsLoading = true;
    let pobjOData = { HeaderData: [], Response: [] };
    pobjOData.HeaderData.push({
      "SMTransactionNo": this.msTrnKey,
      "SMRequestNo": this.msReqNo
    });
    pobjOData.Response.push({
      "ResponseText": "",
      "ErrorCode": ""
    })

    let psJData = JSON.stringify(pobjOData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';
    this.formService.bmlfGetSamplingInfoData(psJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res != null && res.GetSamplingInfoData.length > 0) {
          let pobjSampleData = Object.assign({}, res.GetSamplingInfoData[0]);
          this.mobjSampleInfoModel = pobjSampleData;
        }
      }
    ).add(() => {
      this.mbIsLoading = false;
    })
  }

  //Open LOOKUP MODAL
  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, psTitle) {

    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    if (psTitle != "") {
      this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psTitle);
    } else {
      this.mobjLookupKeyData.title = psTitle;
    }
    if (psEvent == 'blur' && (psValue == undefined || psValue == '' || this.msLookupField == psValue)) {
      this.msLookupField = '';
      if (psValue == undefined || psValue == '') {
        this.mobjSampleInfoModel[psField] = "";
      }
      return false
    }
    let psLookup = "";
    psLookup = psLookUpKey;

    let pobjReqData: any = {
      ScreenName: psScreenName,
      LookUpKey: psLookup,
      Filter1: "",
      Filter2: "",
      Filter3: "",
      Filter4: "",
      Filter5: ""
    }

    this.utilService.bmgfOnKeyInputDisable();
    this.mbIsLoading = true;
    this.lookupService.getLookup(pobjReqData).subscribe(res => {
      this.utilService.bmgfOnKeyInputEnable();
      this.mbIsLoading = false;
      if (res == null) {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.NODATA'));
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        res.LookupData.forEach((ele) => {
          Object.keys(ele).forEach((key) => {
            let psKey = key.toUpperCase();
            if (psKey.search('DATE') != -1) {
              if (ele[key] && ele[key] != '') {
                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
              } else {
                ele[key] = '';
              }
            }
          });
        })
        this.marrLookupGridData = res.LookupData;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;

        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue.toString();
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let psInputKey = this.bmlfGetInputColumn(psField);
          let parrFilterData: any = [];
          parrFilterData = this.marrLookupGridData.filter(obj => {
            let tempValue = obj[psInputKey] == null || obj[psInputKey] == '' ? '' : typeof obj[psInputKey] == 'number' ? obj[psInputKey] : obj[psInputKey].toUpperCase();
            if (tempValue == psValue.toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterData.length == 1) {
            this.mobjLookupKeyData.value = parrFilterData[0];
            this.mobjLookupKeyData.valueKey = parrFilterData[0][psInputKey];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }
      } else {
        this.mbIsLookupModal = false;
        this.mbIsLoading = false;
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.NODATA'));
      }
    }, err => {
      this.mbIsLoading = false;
      console.log(err);
    });
  }

  //Close LOOKUP MODAL
  bmlfCloseLookupModal(pobjData) {
    this.mbIsLookupModal = pobjData.isLookupModal;
    if (pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        if (pobjData.searchValue == null) {
          pobjData.searchValue = '';
        }
        parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue.toUpperCase());
      }
      // console.log(pos);
      if (parrFilterData.length == 0) {
        if (document.getElementById(pobjData.eleId) != undefined) {
          (<HTMLInputElement>document.getElementById(pobjData.eleId)).value = '';
        }
        if (this.mobjSampleInfoModel[pobjData.field] != undefined) {
          this.mobjSampleInfoModel[pobjData.field] = '';
          setTimeout(function () { document.getElementById(pobjData.eleId).focus(); }, 10);
        }
      }
    }
    let elementExists = document.getElementById(pobjData.eleId);
    if (elementExists) {
      document.getElementById(pobjData.eleId).focus();
    }
  }

  bmlfSaveLookupField(pobjData) {
    this.mbIsLookupModal = false;
    this.mbIsChangeData = true;
    this.mbIsLoading = false;
    this.mobjSampleInfoModel[pobjData.field] = pobjData.valueKey
  }

  // get Lookup Value Column
  bmlfGetInputColumn(psField) {
    if (psField == 'smSampleSizeUOM') {
      return 'UomKey';
    } else if (psField == 'smBatchNo') {
      return 'Batchno';
    } else if (psField == 'OrdNo') {
      return 'OrdNo';
    } else if (psField == 'smFormulaID') {
      return 'FormulaId';
    }
  }

  // Global focus Fn
  bmlfOnGlobalFocus(field, value) {
    if (typeof value === 'object' && value.constructor === Object) {
      let valueCopy = Object.assign({}, value);
      this.msLookupField = valueCopy[field];
    } else {
      this.msLookupField = value;
    }
  }

  bmlfOnFormFieldChange() {
    this.mbIsChangeData = true;
  }
  bmlfOpenErrorBlock(psMsg): void {
    this.toastMessageService.notifyError(this.utilService.bmgfonTranslate(psMsg));
  }

  bmlfValidData() {
    if (CustomValidator.bmgfNullCheckValidator(this.mobjSampleInfoModel.smSampleDescription)) {
      this.bmlfOpenErrorBlock("SAMPLETRANS.MSG.ENTERSAMPLEDESC");
      return false;
    } else {
      return true;
    }
  }
  bmlfSubmitForm(psType) {
    if (this.mbIsChangeData == false && this.FieldExtender.mbChange == false && this.customCtrl.mbChange == false && this.msMode == 'U') {
      return;
    }
    if (!this.bmlfValidData()) {
      return;
    }
    //Validate Field Extender -- Start
    let resultString = this.FieldExtender.bmgfValidationCheck();
    if (resultString.value == false) {
      this.toastMessageService.notifyError(resultString.msg);
      return;
    }
    //Customization
    let data = this.utilService.bmgfCustomModelTransform(this.customCtrl);
    this.bmlfonAssignCustValues(data);
    if (this.customCtrl != undefined) {
      if (!this.customCtrl.bmgIsMandatary(this.mobjSampleInfoModel)) {
        return;
      }
    }
    this.bmlfSaveSampleInfoData(psType);
  }
  bmlfSaveSampleInfoData(psType): void {
    this.mbIsLoading = true;
    let pobjData: any = Object.assign({}, this.mobjSampleInfoModel);
    pobjData.SMTransactionNo = this.msTrnKey;
    pobjData.smAnticipatedSalesVolume = CustomValidator.bmgfNullCheckValidator(pobjData.smAnticipatedSalesVolume) ? 0 : pobjData.smAnticipatedSalesVolume;
    pobjData.smCurrentPrice = CustomValidator.bmgfNullCheckValidator(pobjData.smCurrentPrice) ? 0 : this.utilService.bmgfSetPrecision(pobjData.smCurrentPrice, this.miPricePrecision);
    pobjData.smSampleSize = CustomValidator.bmgfNullCheckValidator(pobjData.smSampleSize) ? 0 : this.utilService.bmgfSetPrecision(pobjData.smSampleSize, this.miQtyPrecision)
    pobjData.smTargetPrice = CustomValidator.bmgfNullCheckValidator(pobjData.smTargetPrice) ? 0 : this.utilService.bmgfSetPrecision(pobjData.smTargetPrice, this.miPricePrecision);
    pobjData.smTimeRequired = CustomValidator.bmgfNullCheckValidator(pobjData.smTimeRequired) ? 0 : this.utilService.bmgfSetPrecision(pobjData.smTimeRequired, 2);
    pobjData.smSamplePices = CustomValidator.bmgfNullCheckValidator(pobjData.smSamplePices) ? 0 : pobjData.smSamplePices;
    let parrHeaderData = [];
    parrHeaderData.push(pobjData);
    let pobjJData = {
      ParameterData: [{ "Mode": this.msMode == "I" ? "Insert" : "Update" }],
      GetSamplingInfoData: parrHeaderData,
      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };

    this.formService.bmlfSaveSamplingInfoData(pobjJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.mbIsChangeData = false;
          this.FieldExtender.mbChange = false;
          this.customCtrl.mbChange = false;
          if (res.Response[0].ResponseText.includes("Unable")) {
            this.toastMessageService.notifyError(res.Response[0].ResponseText);
          } else {
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.SAVE"));
            if (this.msMode == "I") {
              this.msMode = "U";
              if (res.GetSamplingInfoData.length > 0) {
                this.msReqNo = res.GetSamplingInfoData[0].SMRequestNo;
                this.mskeyValue = this.msTrnKey + this.msReqNo;
                let pobj = { type: "GetRequestNo", ReqNo: res.GetSamplingInfoData[0].SMRequestNo, SampleDescription: res.GetSamplingInfoData[0].smSampleDescription }
                this.bmgfShowHeaderEvnt.emit(pobj)
              }
            }
            // FieldExtender -- start update
            let screenKeyValue = this.msScreenName + " — " + this.mskeyValue;
            let jsonData = this.FieldExtender.bmlfSaveData(screenKeyValue);
            if (jsonData.length > 0) {
              this.FieldExtender.bmlfCallApi(jsonData).then(res => {
                if (res != null) { }
              });
            };
            // FieldExtender -- End update
            if (psType == "New") {
              let pobj = { type: "New" }
              this.bmgfShowHeaderEvnt.emit(pobj);
            } else if (psType == "Search") {
              let pobj = { type: "Search" }
              this.bmgfShowHeaderEvnt.emit(pobj);
            } else if (psType == "TabChange") {
              let pobj = { type: "TabChange" }
              this.bmgfShowHeaderEvnt.emit(pobj);
            } else if (psType == "AddSample") {
              this.msMode = "I";
              this.msReqNo = "";
              this.mobjSampleInfoModel = new SampleInfoModel();
              this.bmlfGetRequestNo();
            } else if (psType == "CopyRequest") {
              let pobj = { type: "CopyRequest" }
              this.bmgfShowHeaderEvnt.emit(pobj);
            } else if (psType == "Close") {
              this.bmlfCloseForm();
            } else {
              // if (this.msMode == "I") {
              //   this.msMode = "U";
              //   if (res.GetSamplingInfoData.length > 0) {
              //     this.msReqNo = res.GetSamplingInfoData[0].SMRequestNo;
              //   }
              // }
              this.bmlfGetSampleInfoData();
            }
          }
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText)
        }
        console.log(res);
      }
    ).add(() => { this.mbIsLoading = false; })
  }
  bmlfCloseForm() {
    this.router.navigate(["/main/srm/sample-transaction"]);
  }

  bmlfDescriptionModal(event, field) {
    if (event == 'open') {
      this.msCommentField = field;
      if (field == "smSampleDescription") {
        this.msCommentMaxlength = 500;
      } else {
        this.msCommentMaxlength = 5000;
      }
      this.CommentDescription = this.mobjSampleInfoModel[field];
      this.mbCommentDialog = true;
    } else if (event == 'close') {
      this.mobjSampleInfoModel[this.msCommentField] = this.CommentDescription;
      this.CommentDescription = "";
      this.mbCommentDialog = false;
    }
  }

  bmlfGetDefault() {
    let pobjReqData = {
      ScreenName: 'PaymentEntryComponent',
      LookUpKey: 'HomeCurrencyDesc'
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.msHomeCurrency = res.LookupData[0].HomeCurrency;
      }
    });
  }
  bmlfonAssignCustValues(custom) {
    this.mobjSampleInfoModel.CUSTOM1 = custom.CUSTOM1 ? custom.CUSTOM1 : null;
    this.mobjSampleInfoModel.CUSTOM2 = custom.CUSTOM2 ? custom.CUSTOM2 : null;
    this.mobjSampleInfoModel.CUSTOM3 = custom.CUSTOM3 ? custom.CUSTOM3 : null;
    this.mobjSampleInfoModel.CUSTOM4 = custom.CUSTOM4 ? custom.CUSTOM4 : null;
    this.mobjSampleInfoModel.CUSTOM5 = custom.CUSTOM5 ? custom.CUSTOM5 : null;
    this.mobjSampleInfoModel.CUSTOM6 = custom.CUSTOM6 ? custom.CUSTOM6 : null;
    this.mobjSampleInfoModel.CUSTOM7 = custom.CUSTOM7 ? custom.CUSTOM7 : null;
    this.mobjSampleInfoModel.CUSTOM8 = custom.CUSTOM8 ? custom.CUSTOM8 : null;
    this.mobjSampleInfoModel.CUSTOM9 = custom.CUSTOM9 ? custom.CUSTOM9 : null;
    this.mobjSampleInfoModel.CUSTOM10 = custom.CUSTOM10 ? custom.CUSTOM10 : null;
    this.mobjSampleInfoModel.User1 = custom.User1 ? custom.User1 : "";
    this.mobjSampleInfoModel.User2 = custom.User2 ? custom.User2 : "";
    this.mobjSampleInfoModel.User3 = custom.User3 ? custom.User3 : "";
    this.mobjSampleInfoModel.User4 = custom.User4 ? custom.User4 : "";
    this.mobjSampleInfoModel.User5 = custom.User5 ? custom.User5 : "";
    this.mobjSampleInfoModel.User6 = custom.User6 ? custom.User6 : null;
    this.mobjSampleInfoModel.User7 = custom.User7 ? custom.User7 : 0;
    this.mobjSampleInfoModel.User8 = custom.User8 ? custom.User8 : 0;
    this.mobjSampleInfoModel.User9 = custom.User9 ? custom.User9 : 0;
    this.mobjSampleInfoModel.User10 = custom.User10 ? custom.User10 : 0;
    this.mobjSampleInfoModel.User11 = custom.User11 ? custom.User11 : 0;
    this.mobjSampleInfoModel.User12 = custom.User12 ? custom.User12 : 0;
  }
}
