import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';


@Injectable({
    providedIn: 'root'
})

export class SamplePropertyMasterService {
    constructor(
        private httpExt: HttpCleintExtService,
        private appSettings: AppSettingsService
    ) { }
    public bmlfGetSamplePropertyData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GETSamplePropertyMaster`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSaveSamplePropertyData(headerData: any, formMode) {
        if (formMode == 'I') {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveSamplePropertyMaster`;
            return this.httpExt.post<any>(url, headerData, null);
        }
        else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdatesamplePropertyMaster`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }
    public bmlfDeleteSamplePropertyData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteSamplePropertymaster`;
        return this.httpExt.post<any>(url, headerData, null);
    }

}