import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';


@Injectable({
    providedIn: 'root'
})

export class SampleManPowerMasterService {
    constructor(
        private httpExt: HttpCleintExtService,
        private appSettings: AppSettingsService
    ) { }
    public bmlfGetSampleManPower(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetSampleManPowerMaster`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfSaveSampleManPower(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveSampleManPowerMaster`;
            return this.httpExt.post<any>(url, headerData, null);
        }
        else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdateSampleManPowerMaster`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }

    public bmlfDeleteSampleManPower(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteSampleManPowerMaster`;
        return this.httpExt.post<any>(url, headerData, null);
    }
}