import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class srmNpdCrmSetupService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfGetSrmNpdCrmSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetSRMNPDCRMSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfUpdateSrmNpdCrmSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveSRMNPDCRMSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
}