import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard, WMSAuthGuard } from 'src/app/core/guards/auth.guard';
import { SampleTransactionListComponent } from './components/sample-transaction/sample-transaction-list/sample-transaction-list.component';
import { SampleTransactionManageComponent } from './components/sample-transaction/sample-transaction-manage/sample-transaction-manage.component';
import { SRMComponent } from './srm.component';
import { SampleTemplateMasterManageComponent } from './components/sample-template-master/sample-template-master-manage/sample-template-master-manage.component'
import { SampleTemplateMasterListComponent } from './components/sample-template-master/sample-template-master-list/sample-template-master-list.component'
import { SamplePropertyListComponent } from './components/sample-property-master/sample-property-list/sample-property-list.component';
import { SamplePropertyManageComponent } from './components/sample-property-master/sample-property-manage/sample-property-manage.component';
import { SampleGroupListComponent } from './components/sample-group-master/sample-group-list/sample-group-list.component';
import { SampleGroupManageComponent } from './components/sample-group-master/sample-group-manage/sample-group-manage.component';
import { PrintProductCostSheetReportComponent } from './components/srm-report/print-product-cost-sheet-report/print-product-cost-sheet-report.component'
import { SampleManPowerListComponent } from './components/sample-man-power-master/sample-man-power-list/sample-man-power-list.component';
import { SampleManPowerManageComponent } from './components/sample-man-power-master/sample-man-power-manage/sample-man-power-manage.component';
import { PurchaseApproverGroupListComponent } from '../purchase/components/purchase-approver-group/purchase-approver-group-list/purchase-approver-group-list.component';
import { PurchaseApproverGroupManageComponent } from '../purchase/components/purchase-approver-group/purchase-approver-group-manage/purchase-approver-group-manage.component';
import { SampleMaterialWhereUsedReportComponent } from './components/srm-report/sample-material-where-used-report/sample-material-where-used-report.component';
import { SampleCustomComboValuesComponent } from './components/sample-custom-combo-values-master/sample-custom-combo-values.component';
import { PurchasePriceApprovalComponent } from './components/purchase-price-approval/purchase-price-approval.component';
import { SrmNpdCrmSetupComponent } from './components/srm-npd-crm-setup/srm-npd-crm-setup.component';
const routes: Routes = [
  {
    path: '',
    component: SRMComponent,
    children:
      [
        {
          path: '', redirectTo: 'srm', pathMatch: 'full'
        }, {
          path: 'sample-transaction', component: SampleTransactionListComponent, canActivate: [AuthGuard]
        }, {
          path: 'sample-transaction/manage', component: SampleTransactionManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-template-master', component: SampleTemplateMasterListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-template-master/manage', component: SampleTemplateMasterManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-group', component: SampleGroupListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-group/manage', component: SampleGroupManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-property', component: SamplePropertyListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-property/manage', component: SamplePropertyManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-product-cost-sheet-report', component: PrintProductCostSheetReportComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-man-power', component: SampleManPowerListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-man-power/manage', component: SampleManPowerManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-approver-group', component: PurchaseApproverGroupListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-approver-group/manage', component: PurchaseApproverGroupManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-material-where-used-report', component: SampleMaterialWhereUsedReportComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-custom-combo-values', component: SampleCustomComboValuesComponent, canActivate: [AuthGuard]
        },
        {
          path: 'sample-purchase-price-approval', component: PurchasePriceApprovalComponent, canActivate: [AuthGuard]
        },
        {
          path: 'npd-crm-setup', component: SrmNpdCrmSetupComponent, canActivate: [AuthGuard]
        }

      ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SRMRoutingModule { }