import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { SRMReportService } from '../../../http/srm-report.service';
import { AlertService } from 'src/app/core/services/alert.service';
import { Router } from '@angular/router';
import * as moment from 'moment';
import { SampleMaterialWhereUsedReport } from '../../../models/sample-material-where-used-report';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';

@Component({
  selector: 'app-sample-material-where-used-report',
  templateUrl: './sample-material-where-used-report.component.html',
  styleUrls: ['./sample-material-where-used-report.component.scss']
})
export class SampleMaterialWhereUsedReportComponent implements OnInit {
  public screenModeUpdate: string;
  public msServeDateFormat: string = CommonConstants.gsServerDateFormat;
  public msCustomDateFormat: string = "";
  public loaderEnabled: boolean;
  public msCommonMsgFrom: any;
  public msJournalBatchSeries = '';
  public miNumberPrecision: number = 0;
  public miNumberPrecisionFormat: string;
  public getDataList(): void { }
  public gridDataList: any;
  public mobjUserData: any = {};
  public mbLoaderEnabled: boolean = false;
  public mobjModel: SampleMaterialWhereUsedReport = new SampleMaterialWhereUsedReport();
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public mbIsLoading: boolean = false;
  public msLookupField: any = '';
  public msScreenName: string = 'Sample Material Where Used Report';
  public msLookupScreenName = 'Sample Material Where-Used Report';
  public msReportScreenName = 'Sample Material Where Used Report';
  public mbMutiSelectionLookup = true;
  public msCommonMsg: any = '';
  public paramValueArray = [];
  public parameterValue: any = '';
  public checkval = false;
  public mbIsValid = false;
  /**-------START: TASK Field ----*/
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  public mbIsActive = false;
  // precision
  public miQtyPrecision: number = 0;
  public miQtyPrecisionFormat: string = 'n' + this.miQtyPrecision;
  public mnMarginOne: Number = 0;
  /**-------END: TASK Field ----*/

  private paramKey = {
    ItemKeyFrom: 'txtItemKeyFrom',
    ItemKeyTo: 'txtItemKeyTo',
  }
  @ViewChild('dataListCtrl') dataListCtrl: ElementRef;
  @ViewChild('paramCtrl') controls: ElementRef;
  constructor(
    private lookupService: LookupService,
    public dateFormatService: DateFormatService,
    private lsService: LocalStorageService,
    private router: Router,
    public utilService: AppUtility,
    public toastMessageService: ToastMessageService,
    private alertservice: AlertService,
    public service: SRMReportService
  ) {

  }

  /**
   * On Init Component
   */
  ngOnInit() {
    this.utilService.getBMEScreePermission(this.msScreenName);
    this.miNumberPrecision = 4;
    this.miNumberPrecisionFormat = 'n' + this.miNumberPrecision;
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    // uncomment later on when link is generated
    this.msCommonMsg = this.utilService.bmgfonTranslate('SAMPLEMATERIALWHEREUSED.MSG.REQTOKEY');
    this.msCommonMsgFrom = this.utilService.bmgfonTranslate('SAMPLEMATERIALWHEREUSED.MSG.REQFROMKEY');
    this.mobjUserData = this.lsService.getUserSession();
    this.getParam(this.msScreenName);

  }
  /**
   * On Order Action
   */
  bmlfOnSaveDetails() {
    if (!this.utilService.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return false;
    }

    let isMendentFileds = this.bmlfValidateData();
    if (isMendentFileds) {
      return false;
    }

    // if (!this.mbIsValid) {
    //   return false;
    // }

    this.onProcess();

  }

  //Open LOOKUP MODAL
  bmlfonOpenLookupModal(psField, psEvent, psValue, psType, psEleId, piRowIdx = null) {
    
    if (psEvent == 'blur' && this.msLookupField == psValue) {
      this.msLookupField = '';
      this.mobjModel[psField + 'Desc'] = '';
      return false;
    }

    this.mbMutiSelectionLookup = false;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjModel[psField + 'Desc'] = '';
      return false
    }


    let psLookUpKey = this.bmlfonSetLookupKey(psField);
    let psLookupTitle = this.bmlfSetLookupTitle(psField);
    let psScreenName = this.msLookupScreenName;
    let pobjReqData: any = {}
    if (psField == 'ItemKeyFrom' || psField == 'ItemKeyTo') {
      pobjReqData = { ScreenName: psScreenName, LookUpKey: psLookUpKey, FIlter1: this.mobjModel.ItemKeyFrom, FIlter2: this.mobjModel.ItemKeyTo }
    }
    else {
      pobjReqData = { ScreenName: psScreenName, LookUpKey: psLookUpKey }
    }


    // this.utilService.bmgfOnKeyInputDisable();
    this.mbIsLoading = true;
    this.lookupService.getLookup(pobjReqData).subscribe(res => {

      setTimeout(() => {
        this.utilService.bmgfOnKeyInputEnable();
        this.mbIsLoading = false;
      }, 500);

      if (res == null) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATA'));

        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        this.marrLookupGridData = res.LookupData;
        this.mobjLookupKeyData.field = psField;
        res.LookupData.forEach((ele) => {
          Object.keys(ele).forEach((key) => {
            let psKey = key.toUpperCase();
            if (psKey.search('DATE') != -1) {
              if (ele[key] && ele[key] != '') {
                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
              } else {
                ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
              }
            }

          });
        })
        this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psLookupTitle);
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;
        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue;
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {

          let psInputKey = this.bmlfGetInputColumn(psField);
          let parrFilterData: any = [];
          parrFilterData = this.marrLookupGridData.filter(obj => {
            let tempValue;
            tempValue = obj[psInputKey] == null || obj[psInputKey] == '' ? '' : (obj[psInputKey].toString()).toUpperCase();
            if (psInputKey == 'Lang') {
              if (tempValue.trim() == psValue.toUpperCase()) {
                return obj;
              }
            } else {
              if (tempValue.trim() == psValue.toUpperCase()) {
                return obj;
              }
            }
          });

          if (parrFilterData.length == 1) {
            this.mobjLookupKeyData.value = parrFilterData[0]
            this.bmlfonSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else if (parrFilterData.length == 0) {

            this.mobjModel[psField] = '';
            let elementExists = document.getElementById(psEleId);
            if (elementExists) {
              document.getElementById(psEleId).focus();
            }
            this.mbIsLookupModal = true;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }
      } else {
        this.mobjModel[psField] = '';
        if (psEleId) {

          document.getElementById(psEleId).focus();
        }
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATA'));
      }
    }, err => {
      console.log(err);
    });
  }

  //Close LOOKUP MODAL
  bmlfonCloseLookupModal(pobjData) {
    
    this.mbIsLookupModal = pobjData.isLookupModal;
    // let psInputDesKey = this.bmlfGetInputDesColumn(pobjData.field);
    if (pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        parrFilterData = pobjData.gridData.filter(obj => (obj[psInputKey].toString()).toUpperCase() == pobjData.searchValue ? pobjData.searchValue.toUpperCase() : pobjData.searchValue);
      }
      if (parrFilterData.length == 0) {
        this.mobjModel[pobjData.field] = '';

      }
      document.getElementById(pobjData.eleId).focus();
    }
  }

  bmlfonSaveLookupField(event) {
    
    let psInputKey = this.bmlfGetInputColumn(event.field);
    let str = event.field.slice(-4);
    let insertName: any;
    if (this.trimCheck(str)) {
      insertName = event.field.slice(0, event.field.length - 4);
      if (this.mobjModel[insertName + 'To'] && this.mobjModel[insertName + 'To'] < event.valueKey) {
        this.alertservice.infoAlert('Alert', 'From value cannot be Greater than To value');
        this.mobjModel[event.field] = '';
        this.mbIsLookupModal = false;
        this.mbIsValid = false;
        return;
      }
    }

    else if (event.field == "ItemKeyTo") {
      if (event.value.ItemKey < this.mobjModel.ItemKeyFrom){
        this.alertservice.infoAlert('Alert', 'To value cannot be less than From value');
        this.mbIsLookupModal = false;
        this.mbIsValid = false;
        this.mobjModel.ItemKeyTo = '';
        //document.getElementById('txtItemTo').focus();
        return;
      }
    }
    else if (event.field == "ItemKeyFrom") {
      if (event.value.ItemKey > this.mobjModel.ItemKeyTo){
        this.alertservice.infoAlert('Alert', 'From value cannot be Greater than To value');
        this.mbIsLookupModal = false;
        this.mbIsValid = false;
        this.mobjModel.ItemKeyFrom = '';
        //document.getElementById('txtItemFrom').focus();
        return;
      }
    }
    
    else {
      insertName = event.field.slice(0, event.field.length - 2);
      if (this.mobjModel[insertName + 'From'] && this.mobjModel[insertName + 'From'] > event.valueKey) {
        this.alertservice.infoAlert('Alert', 'To value cannot be less than From value');
        this.mobjModel[event.field] = '';
        this.mbIsLookupModal = false;
        this.mbIsValid = false;
        return;
      }
    }



    let psInputDesKey = this.bmlfGetInputDesColumn(event.field);
    this.mbIsLookupModal = false;
    this.mbMutiSelectionLookup = false;
    if (!Array.isArray(event.value)) {
      if (event.type == 'custom') {
        this.mobjModel[event.field] = event.value[psInputKey];
        //this.mobjModel[pobjData.field] = pobjData.value[psInputDesKey]
        this.bmlfonGlobalOnChangesFn(event.field, event, null);
      }
      this.mbIsValid = true;
    }

  }
  trimCheck(str) {
    if (str == 'From') { return true; }
    else { return false; }
  }

  // Set Lookup Description Column
  bmlfGetInputDesColumn(psField) {
    if (psField == 'ItemKeyFrom' || psField == 'ItemKeyTo') {
      return 'Desc1'
    }
  }

  // Set Lookup KEY
  bmlfonSetLookupKey(psField) {
    let psLookUpKey;
    if (psField == 'ItemKeyFrom') {
      return psLookUpKey = 'ItemKeyFrom'
    } else if (psField == 'ItemKeyTo') {
      return psLookUpKey = 'ItemKeyTo'
    }
    return psLookUpKey

  }
  bmlfSetLookupTitle(psField) {
    let psLookupTitle;
    if (psField == 'ItemKeyFrom' || psField == 'ItemKeyTo') {
      psLookupTitle = 'SAMPLEMATERIALWHEREUSED.ITEMLOOKUP'
    }
    return psLookupTitle;
  }


  // get Lookup Value Column
  bmlfGetInputColumn(psField) {
    if ((psField == 'ItemKeyFrom' || psField == 'ItemKeyTo')) {
      return 'ItemKey';
    }
  }

  bmlfonGlobalOnChangesFn(psFieldName, pobjFieldData, piIndex = null) {
    
    let str = psFieldName.slice(-4);
    let insertName: any;
    if (this.trimCheck(str)) {
      insertName = psFieldName.slice(0, psFieldName.length - 4);
      if (this.mobjModel[insertName + 'To'] && this.mobjModel[insertName + 'To'] < pobjFieldData.valueKey) {
        this.alertservice.infoAlert('Alert', 'From value cannot be Greater than To value');
        this.mobjModel[psFieldName] = '';
        this.mbIsLookupModal = false;
        return;
      }
    }
    else {
      insertName = psFieldName.slice(0, psFieldName.length - 2);
      if (this.mobjModel[insertName + 'From'] && this.mobjModel[insertName + 'From'] > pobjFieldData.valueKey) {
        this.alertservice.infoAlert('Alert', 'To value cannot be less than from value');
        this.mobjModel[psFieldName] = '';
        this.mbIsLookupModal = false;
        return;
      }
    }
  }
  // Global focus Fn
  bmlfOnGlobalFocus(field, value) {
    if (typeof value === 'object' && value.constructor === Object) {
      let valueCopy = Object.assign({}, value);
      this.msLookupField = valueCopy[field];
    } else {
      this.msLookupField = value;
    }
  }
  bmlfValidateData(evenType?) {
    let isMendentFiled = false;
    return isMendentFiled;
  }
  onProcess() {
    
    this.loaderEnabled = true;
    let pDefaultReportType = localStorage.getItem('reportType');
    let reportObj = {};

    reportObj = {
      'ReportName': this.msReportScreenName,
      "ItemKeyFrom": this.mobjModel.ItemKeyFrom,
      "ItemKeyTo": this.mobjModel.ItemKeyTo,
      'PrinterParameter': '',
      'ListingFlag': false,
      'ListingVariable': '',
      'Parameter': this.parameterValue
    }
    this.utilService.bmgfReportAction(reportObj, this.msReportScreenName).then((res: any) => {
      this.loaderEnabled = false;
      if (res != null) {
        if (res.Response[0].ErrorCode == "") {
          let pPDFSrc = res.Response[0].Pdfpath;
        }
        else {
          if (res.Response[0].ErrorCode == '2001') {
            this.toastMessageService.notifyError(res.Response[0].ResponseText);
          } else if (res.Response[0].ErrorCode != '' && res.Response[0].ErrorCode != '2001') {
            this.onOpenErrorBlock(res.Response[0].ErrorCode)
            //this.toastMessageService.notifyError(res.Response[0].ErrorCode);
          }
          else {
            this.onOpenErrorBlock("No Data Found")
            //this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.NODATA'));

          }
          this.loaderEnabled = false;
        }
      }
    }, error => {
      this.loaderEnabled = false;
    });
  }

  onOpenErrorBlock(msg) {
    this.alertservice.infoAlert('Alert', '<div class="about-list">' + msg + '</div>');
  }
  onParamchange(e) {
    try {
      let pValue = e.target.value;
      let optionArray = [...this.dataListCtrl.nativeElement.options];
      optionArray.some(res => {
        if (pValue == res.value) {
          this.fetchParamData(pValue)
          return true;
        }
      })
    }
    catch (e) {
      console.log(e);
      console.trace();
    }
  }
  private fetchParamData(param) {
    let pValue = param;
    this.loaderEnabled = true;
    this.service.getReportParameterValue(this.msScreenName, param).subscribe(res => {
      if (res != null && res.RPTPARAMETERSBYVALUE.length > 0) {
        this.clearModel();
        this.fillControlFromParam(res.RPTPARAMETERSBYVALUE);
      }
      else {
        console.error('No Response');
        console.trace();
        this.loaderEnabled = false;
      }
    })
  }
  private fillControlFromParam(resData) {
    let selectedData: any;
    console.log("res data", resData)
    resData.forEach(element => {
      selectedData = this.paramKey;
    })
    resData.forEach(element => {
      let key = this.getKeyFromValue(element.CONTROLNAME, selectedData);

      if (key == 'StartDate' || key == 'EndDate') {
        this.mobjModel[key] = new Date(element.CONTROLVALUE)
      } else if (key == 'UpdatedToFinance' && element.CONTROLVALUE == 'updateToFinance') {
        this.mobjModel[key] = true;
      } else if (key == 'UpdatedToFinance' && element.CONTROLVALUE == 'update') {
        this.mobjModel[key] = false;
      } else if (element.CONTROLNAME == 'txtItemFrom' || element.CONTROLNAME == 'txtItemTo') {
        this.mobjModel[key] = Number(element.CONTROLVALUE)
      }
      else {
        this.mobjModel[key] = element.CONTROLVALUE;
      }
    });
    //scr 152828

    this.loaderEnabled = false;
  }
  private getKeyFromValue(value, selectedData) {
    let tempKey = Object.keys(this.mobjModel).find(data => {
      if (data != 'orderType') {
        if (value == 'Option1' || value == 'Option2') {
          return selectedData['SelectReport'] == value
        }
        else {
          return selectedData[data] == value
        }

      }
    });
    return tempKey;
  }
  private clearModel() {
    Object.keys(this.mobjModel).forEach(keys => {
      if (keys == 'StartDate' || keys == 'EndDate') {
        this.mobjModel[keys] = new Date();
      }
      else {
        this.mobjModel[keys] = " ";
      }
    })
  }
  private getParam(scr) {
    this.loaderEnabled = true;
    this.service.getReportParameter(scr).subscribe(res => {
      if (res != null) {
        this.paramValueArray = res.RPTPARAMETERS;
        this.loaderEnabled = false;

        //scr 152839

      }
      else {
        console.trace();
        console.error("Res Is Null");
        this.loaderEnabled = false;
      }
    })
  }
  onParameterSave() {
    let paramValue = this.parameterValue;
    let newObj = this.mobjModel;
    let paramKey = this.paramKey;
    if (paramValue == '') {
      this.onOpenErrorBlock('Parameter cannot be empty');
      // this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('Parameter Cannot be Blank'));            
      return;
    }
    else {
      this.mbIsLoading = true;
      this.screenModeUpdate = "I";
      let jObj = {};
      let headerData = [];
      let jArray = [];
      let counterRecId = 0;
      // let oArray = [...this.dataListCtrl.nativeElement.options];
      jObj["ParamName"] = paramValue;
      jObj["ScreenName"] = this.msScreenName;


      if (this.paramValueArray.length > 0) {
        let list = this.paramValueArray.filter(x => x.PARANAME === paramValue)[0];
        if (list != undefined) {
          this.screenModeUpdate = 'U';
          jObj["UpdateMode"] = this.screenModeUpdate;
        } else {
          jObj["UpdateMode"] = this.screenModeUpdate;
        }
      } else {
        jObj["UpdateMode"] = this.screenModeUpdate;
      }

      headerData.push(jObj);
      Object.keys(newObj).map(key => {

        let tempJObj = {};
        console.log("key", key, "value", newObj[key])
        if (key == "StartDate") {
          tempJObj["ControlName"] = paramKey[key]
          tempJObj["ControlValue"] = moment(newObj[key]).format(CommonConstants.gsServerDateFormat); //scr 160910
          tempJObj["RecId"] = counterRecId++;
        }
        else if (key == "EndDate") {
          tempJObj["ControlName"] = paramKey[key]
          tempJObj["ControlValue"] = moment(newObj[key]).format(CommonConstants.gsServerDateFormat); //scr 160910
          tempJObj["RecId"] = counterRecId++;
        } else if (key == "UpdatedToFinance") {
          tempJObj["ControlName"] = paramKey[key]
          tempJObj["ControlValue"] = newObj[key] == true ? 'updateToFinance' : 'update' //scr 160910
          tempJObj["RecId"] = counterRecId++;
        }
        else {
          tempJObj["ControlName"] = this.paramKey[key]
          tempJObj["ControlValue"] = newObj[key]
          tempJObj["RecId"] = counterRecId++;
        }

        jArray.push(tempJObj);

        //  }
      });
      this.service.saveReportParameters(jArray, headerData).subscribe(res => {
        if (res != null) {
          if (res['Response'][0].ResponseText == "Sucess") {
            //this.paramCtrl.nativeElement.value = "";
            this.onOpenErrorBlock('Saved Successfully.');
            //this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('Parameter Saved Successfully'));
            this.parameterValue = '';
            this.mbIsLoading = false;
            this.getParam(this.msScreenName);
          }
          else {
            alert('Cant Save');
            console.trace();
            console.error('Save error');
            this.mbIsLoading = false;
          }
        }
      })
    }

  }
  onParameterDelete() {
    //let oParaValue = this.paramCtrl.nativeElement.value;
    let oParaValue = this.parameterValue;
    if (oParaValue == "") {
      this.onOpenErrorBlock('Select Report Parameter.');
      //this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('Select Report Parameter'));
      return;
    }
    else {
      let oArray = [...this.dataListCtrl.nativeElement.options];
      if (oArray.length != 0) {
        oArray.some(res => {
          if (res.value == oParaValue) {
            this.checkval = false;
            this.loaderEnabled = true;
            this.deleteApiParam(oParaValue);
            this.parameterValue = '';
            return true
          }
          else {
            this.checkval = true;
            return false;
          }

        })
      }
      else {
        this.onOpenErrorBlock('Invalid Parameter');
        //this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('Invalid Parameter'));
        return;
      }
      if (this.checkval == true) {
        this.onOpenErrorBlock('Invalid Paramenter');
        //this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('Invalid Parameter'));
      }
    }
  }
  private deleteApiParam(oParaValue) {
    this.service.DeleteReportParameter(this.msScreenName, oParaValue).subscribe(res => {
      if (res != null) {
        if (res.Response[0].ResponseText == "Success") {
          this.onOpenErrorBlock('Deleted Successfully.');
          //this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('Deleted Successfully'));
          //this.paramCtrl.nativeElement.value = "";
          this.parameterValue = '';
          this.clearModel();
          this.getParam(this.msScreenName);
          this.loaderEnabled = false;
          return true;
        }
      }

    })
  }

  onclose() {
    this.router.navigate(['main']);
  }
  //Open Task MODAL
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = "0";
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

}

