import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
  providedIn: 'root'
})
export class PurchasePriceApprovalService {

  constructor(
    private httpExt: HttpCleintExtService,
    private appSettings: AppSettingsService
  ) { }

  public bmlfGetPurchasePriceApprovalMaster(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetPurchasePriceApprovalMaster`;
    return this.httpExt.post<any>(url, headerData, null);
  }

  public bmlfSavePurchasePriceApprovalMaster(headerData: any, formMode) {
    if (formMode == "I") {
      const url = `${this.appSettings.ServerAPIEndpoint}/bme/SavePurchasePriceApprovalMaster`;
      return this.httpExt.post<any>(url, headerData, null);
    }
    else {
      const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdatePurchasePriceApprovalMaster`;
      return this.httpExt.post<any>(url, headerData, null);
    }
  }

  public bmlfApprovedPurchasePriceApprovalMaster(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/ApprovedPurchasePriceApprovalMaster`;
    return this.httpExt.post<any>(url, headerData, null);
  } 

}
