import { Component, OnInit, Renderer2, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { DateFormatService } from 'src/app/core/services/dateFormat.service'
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import * as moment from 'moment';
import { SampleGroupMasterModel } from '../../../models/sample-group-master.model';
import { SampleGroupMasterService } from '../../../http/sample-group-master-service';
@Component({
  selector: 'app-sample-group-manage',
  templateUrl: './sample-group-manage.component.html',
  styleUrls: ['./sample-group-manage.component.scss']
})
export class SampleGroupManageComponent implements OnInit {

  public mbIsLoading: boolean = false;
  public msPrdKey: string;
  public msScreenName: string = 'Sample Group Master';
  public mbChange: boolean = false;
  public msCustomDateFormat: string = "";
  public msFormMode: string = "I";

  /**---Object Initialize-----**/
  public mobjSmplGrpMasterModel: SampleGroupMasterModel = new SampleGroupMasterModel();
  public mobjUserData: any = {};
  public mbShowDeleteDialog: boolean = false;

  /**---Lookup realted field----**/
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public mnumLineItemRowIndex: number = -1;
  /**---END : Lookup realted field----**/

  public mbSaveDialogView: boolean = false;
  public mbIsSearchMode: boolean = false;
  public mbIsAddMode: boolean = false;
  public mbIsCopyMode: boolean = false;
  public mbIsDeleteMode: boolean = false;
  public mbIsCloseMode: boolean = false;
  public msCopyRecord: string = "N";
  public mbCustomControl = false;
  public mbDescriptionDialog: boolean = false;
  public mbImage:boolean=false;
  public mskeyValue: string = ""; //FieldExtender

  /**-------START: TASK Field ----**/
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  /**-------END: TASK Field ------**/

  /**-----------START E-Signature Field----------**/
  public mbSignatureModal: boolean = false;
  public mobjSignatureData: any = {};
  public mbIsEsignature: boolean = false;
  public mbIsEsignADTPermision: boolean = false;
  public mbIsEsignAuditDialog: boolean = false;
  public mobjCommonAlertData: any = {};
  public mbCommonAlertDialog: boolean = false;
  /**-----------END E-Signature Field----------**/
  @ViewChild('custom') customCtrl: any;
  @ViewChild('FieldExtender') FieldExtender: any; //FieldExtender

  constructor(private router: Router, public route: ActivatedRoute,
    public renderer: Renderer2,
    private lsService: LocalStorageService,
    private formService: SampleGroupMasterService,
    private toastMessageService: ToastMessageService,
    public dateFormatService: DateFormatService,
    public utilService: AppUtility,
    private lookupService: LookupService,
    public userFieldService: UserFieldService) {
  }
  ngOnInit() {
    
    /*--------- get screen permission --------*/
    this.utilService.getBMEScreePermission(this.msScreenName);
    /**--- Get default date format from service--- */
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    let psPrdKey = this.route.snapshot.queryParamMap.get('prdKey');
    let msCopyRecord = this.route.snapshot.queryParamMap.get('CopyRecord');
    if (CustomValidator.bmgfNullCheckValidator(psPrdKey)) {
      this.msFormMode = "I";
      this.mbCustomControl = true; // Customization
      this.msPrdKey = "";
      this.mskeyValue = this.msPrdKey;
      let pobjCopyData = localStorage.getItem('copySampleGroupData');
      if (pobjCopyData) {
        let pobjGetCopyData: any = JSON.parse(pobjCopyData);
        this.mobjSmplGrpMasterModel = Object.assign({}, pobjGetCopyData.HeaderData);
        this.msCopyRecord = "Y";
        this.mskeyValue = pobjGetCopyData.prdKey;
        localStorage.removeItem('copySampleGroupData');
      }
      setTimeout(() => {
        document.getElementById("txtSmGroupId").focus();
      }, 200);
    } else {
      this.msCopyRecord = CustomValidator.bmgfNullCheckValidator(msCopyRecord) ? "N" : msCopyRecord;
      /**----customization----- */

      this.msFormMode = this.msCopyRecord == "Y" ? "I" : "U";
      this.msPrdKey = psPrdKey;
      this.mskeyValue = this.msPrdKey;
      this.bmlfGetSampleGroupData();
    }
    this.mobjSignatureData = {
      scrrenName: this.msScreenName,
      reasonMinLength: 10,
      reqSignAuth: 2,
      date: moment().format(this.msCustomDateFormat.toUpperCase()),
      userName: this.mobjUserData.UserId
    };
    this.bmlfCheckEsignApplicableOnForm();
  }

  bmlfGetSampleGroupData() {
    this.mbIsLoading = true;
    let pobjOData = { HeaderData: [], Response: [] };
    pobjOData.HeaderData.push({
      "SmGroupId": this.msPrdKey
    });
    pobjOData.Response.push({
      "ResponseText": "",
      "ErrorCode": ""
    })

    let psJData = JSON.stringify(pobjOData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';
    this.formService.bmlfGetSampleGroupData(psJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res != null && res.HeaderData.length > 0) {
          this.mbCustomControl = true;
          this.mobjSmplGrpMasterModel = Object.assign({}, res.SMGroupMaster[0]);
          if (this.msCopyRecord == "Y") {
            this.mobjSmplGrpMasterModel.SmGroupId = "";
            document.getElementById("txtSmGroupId").focus();
          }
        }
        this.bmlfCheckEsignApplicableOnForm();
      }
    ).add(() => {
      this.mbIsLoading = false;
    })
  }
  bmlfOnFormFieldChange() {
    this.mbChange = true;
  }
  bmlfOpenErrorBlock(psMsg): void {
    this.toastMessageService.notifyError(this.utilService.bmgfonTranslate(psMsg));
  }
  /**--------Start: Lookup Functionality----- */
  // LOOKUP MODAL 
  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, psTitle = "", pnumRowIdx = null) {
    
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psTitle);
    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjSmplGrpMasterModel[psField] = "";
      this.mbIsLoading = false;
      return false
    }
    let psReqData = {
      ScreenName: psScreenName, LookUpKey: psLookUpKey, Filter1: "", Filter2: "", Filter3: "", Filter4: '', Filter5: ''
    };

    this.lookupService.getLookup(psReqData).subscribe(res => {
      
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        res.LookupData.forEach((ele) => {
          Object.keys(ele).forEach((key) => {
            let psKey = key.toUpperCase();
            if (psKey.search('DATE') != -1) {
              if (ele[key] && ele[key] != '') {
                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
              } else {
                ele[key] = ''
              }
            }
          });
        })
        this.marrLookupGridData = res.LookupData;
        this.mnumLineItemRowIndex = pnumRowIdx;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;

        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue.toString();
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
          let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }

      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    })
  }

  /** save lookup field data */
  bmlfSaveLookupField(pobjEvent) {
    
    this.mbIsLoading = false;
    this.mbIsLookupModal = false;
    if (pobjEvent.type == 'custom') {
      if (pobjEvent.field == "") {
        this.mobjSmplGrpMasterModel.SmGroupId = pobjEvent.valueKey;
        this.mobjSmplGrpMasterModel.SmGroupDesc = pobjEvent.value.Description;
      }
      this.bmlfOpenSelectedRecord(pobjEvent.value);
    }
  }

  // Open Desc. Dialoge Box.

// Description expanded Modal
bmlfDescriptionModal(event) {
  if (event == 'open') {
    this.mbDescriptionDialog = true;
  } else if (event == 'close') {
    this.mbDescriptionDialog = false;
  }
}


  bmlfCloseLookupModal(pobjEvent): void {
    this.mbIsLookupModal = pobjEvent.isLookupModal;
    document.getElementById(pobjEvent.eleId).focus();
  }
  /**--------End: Lookup Functionality----- */
  bmlfValidData() {
    if (CustomValidator.bmgfNullCheckValidator(this.mobjSmplGrpMasterModel.SmGroupId)) {
      this.bmlfOpenErrorBlock("SAMPLEGROUP.MSG.SGMREQ");
      document.getElementById("txtSmGroupId").focus();
      return false;
    } else {
      return true;
    }
  }
  bmlfonAssignCustValues(custom) {
    this.mobjSmplGrpMasterModel.CUSTOM1 = custom.CUSTOM1 ? custom.CUSTOM1 : null;
    this.mobjSmplGrpMasterModel.CUSTOM2 = custom.CUSTOM2 ? custom.CUSTOM2 : null;
    this.mobjSmplGrpMasterModel.CUSTOM3 = custom.CUSTOM3 ? custom.CUSTOM3 : null;
    this.mobjSmplGrpMasterModel.CUSTOM4 = custom.CUSTOM4 ? custom.CUSTOM4 : null;
    this.mobjSmplGrpMasterModel.CUSTOM5 = custom.CUSTOM5 ? custom.CUSTOM5 : null;
    this.mobjSmplGrpMasterModel.CUSTOM6 = custom.CUSTOM6 ? custom.CUSTOM6 : null;
    this.mobjSmplGrpMasterModel.CUSTOM7 = custom.CUSTOM7 ? custom.CUSTOM7 : null;
    this.mobjSmplGrpMasterModel.CUSTOM8 = custom.CUSTOM8 ? custom.CUSTOM8 : null;
    this.mobjSmplGrpMasterModel.CUSTOM9 = custom.CUSTOM9 ? custom.CUSTOM9 : null;
    this.mobjSmplGrpMasterModel.CUSTOM10 = custom.CUSTOM10 ? custom.CUSTOM10 : null;
    this.mobjSmplGrpMasterModel.User1 = custom.User1 ? custom.User1 : "";
    this.mobjSmplGrpMasterModel.User2 = custom.User2 ? custom.User2 : "";
    this.mobjSmplGrpMasterModel.User3 = custom.User3 ? custom.User3 : "";
    this.mobjSmplGrpMasterModel.User4 = custom.User4 ? custom.User4 : "";
    this.mobjSmplGrpMasterModel.User5 = custom.User5 ? custom.User5 : "";
    this.mobjSmplGrpMasterModel.User6 = custom.User6 ? custom.User6 : null;
    this.mobjSmplGrpMasterModel.User7 = custom.User7 ? custom.User7 : 0;
    this.mobjSmplGrpMasterModel.User8 = custom.User8 ? custom.User8 : 0;
    this.mobjSmplGrpMasterModel.User9 = custom.User9 ? custom.User9 : 0;
    this.mobjSmplGrpMasterModel.User10 = custom.User10 ? custom.User10 : 0;
    this.mobjSmplGrpMasterModel.User11 = custom.User11 ? custom.User11 : 0;
    this.mobjSmplGrpMasterModel.User12 = custom.User12 ? custom.User12 : 0;
  }
  bmlfSubmitData(pbClose: boolean = false): void {
    if (!this.utilService.mbWrite) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
        return;
    };
    if (this.mbChange == false && this.FieldExtender.mbChange == false && this.customCtrl.mbChange != true && (this.msFormMode == 'U')) {
      return;
    }
    if (!this.bmlfValidData()) {
      return;
    }
    //Validate Field Extender -- Start
    let resultString = this.FieldExtender.bmgfValidationCheck();
    if (resultString.value == false) {
      if (resultString.msg != "") {
        this.toastMessageService.notifyError(resultString.msg);
      }
      return;
    }
    if (this.mbIsEsignature) {
      this.bmlfopenESignatureModal();
      return;
    }
    //Customization
    let data = this.utilService.bmgfCustomModelTransform(this.customCtrl);
    this.bmlfonAssignCustValues(data);
     // Bug 170424 - Check Mendetory for Header
     if (this.customCtrl != undefined) {
      if (!this.customCtrl.bmgIsMandatary(this.mobjSmplGrpMasterModel)) {
        return;
      }
    }
    this.bmlfSaveSampleGroupData();
  }

  bmlfFieldExtWarningEvent(event) {
    if (event == true) {
      if (this.mbIsEsignature) {
        this.bmlfopenESignatureModal();
        return;
      }
      //Customization
      let data = this.utilService.bmgfCustomModelTransform(this.customCtrl);
      this.bmlfonAssignCustValues(data)
      this.bmlfGetSampleGroupData();
    }
  }
  bmlfSaveSampleGroupData(): void {
    
    this.mbIsLoading = true;
    let parrHeaderData = [];
    parrHeaderData.push(Object.assign({}, this.mobjSmplGrpMasterModel));
    let pobjJData = {
      HeaderData: parrHeaderData,
      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };

    this.formService.bmlfSaveSampleGroupData(pobjJson, this.msFormMode).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.FieldExtender.mbChange = false;
          this.customCtrl.mbChange = false;
          this.mbChange = false;
          if (this.msFormMode == "I") {
            this.msFormMode = "U";
            this.msCopyRecord = "N";
            this.mobjSmplGrpMasterModel.SmGroupId = res.HeaderData[0].SmGroupId;
            this.msPrdKey = this.mobjSmplGrpMasterModel.SmGroupId;
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.SAVE"));
          } else if (this.msFormMode == "U") {
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.EDIT"));
          }
          // FieldExtender -- start update
          let screenKeyValue = this.msScreenName + " — " + this.msPrdKey;
          let jsonData = this.FieldExtender.bmlfSaveData(screenKeyValue);
          if (jsonData.length > 0) {
            this.FieldExtender.bmlfCallApi(jsonData).then(res => {
              if (res != null) { }
            });
          };
          // FieldExtender -- End update
          if (this.mbIsSearchMode) {
            this.mbIsSearchMode = false;
            this.bmlfOpenLookupModal('SmGroupId', this.msScreenName, 'SMGroupMasterList', 'click', '', 'custom', 'cmdSearch', 'SAMPLEGROUP.GROUPLOOKUP');
          } else if (this.mbIsAddMode) {
            this.mbIsAddMode = false;
            this.bmlfRefreshForm();
          } else if (this.mbIsCopyMode) {
            this.mbIsCopyMode = false;
            this.bmlfCopyData();
          } else if (this.mbIsCloseMode) {
            this.mbIsCloseMode = false;
            this.router.navigate(["/main/srm/sample-group"]);
          } else {
            this.bmlfGetSampleGroupData();
          }
        } else {
          this.mbIsSearchMode = false;
          this.mbIsAddMode = false;
          this.mbIsCopyMode = false;
          this.mbIsDeleteMode = false;
          this.mbIsCloseMode = false;
          this.toastMessageService.notifyError(res.Response[0].ResponseText)
          this.bmlfCheckEsignApplicableOnForm();
        }
        console.log(res);
      }
    ).add(() => { this.mbIsLoading = false; })
  }

  bmlfCloseForm(): void {
    if (this.mbChange == true || this.FieldExtender.mbChange == true || this.customCtrl.mbChange == true) {
      this.mbSaveDialogView = true;
      this.mbIsCloseMode = true;
    } else {
      this.router.navigate(["/main/srm/sample-group"]);
    }
  }
  // So Footer Action finction
  bmlfFooterActionFn(event) {
    if (event == 'SearchEvnt') {
      if (this.mbChange || this.FieldExtender.mbChange == true || this.customCtrl.mbChange == true) {
        this.mbSaveDialogView = true;
        this.mbIsSearchMode = true
        return false;
      }
      this.bmlfOpenLookupModal('SmGroupId', this.msScreenName, 'SMGroupMasterList', 'click', '', 'custom', 'cmdSearch', 'SAMPLEGROUP.GROUPLOOKUP');
    } else if (event == 'AddEvnt') {
      if (!this.utilService.mbWrite) {
          this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
          return false;
      };
      if (!this.bmlfValidData()) {
        return;
      }
      if (this.mbChange || this.FieldExtender.mbChange == true || this.customCtrl.mbChange == true) {
        this.mbSaveDialogView = true;
        this.mbIsAddMode = true
        return false;
      }
      this.bmlfRefreshForm();
    } else if (event == 'DeleteEvnt') {
      if (!this.utilService.mbDelete) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
        return false;
      };
      if (this.mobjSmplGrpMasterModel.SmGroupId == '' && this.msFormMode == "I" && this.msCopyRecord == "N" && !this.mbChange && this.FieldExtender.mbChange == false && this.customCtrl.mbChange == false) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATADELETE'));
        return false;
      }
      if (this.mobjSmplGrpMasterModel.SmGroupId == '' && this.msFormMode == "I" && this.msCopyRecord == "Y" && !this.mbChange && this.FieldExtender.mbChange == false && this.customCtrl.mbChange == false) {
        this.bmlfRefreshForm();
        return false;
      }
      this.mbShowDeleteDialog = true;
    } else if (event == 'CopyEvnt') {
      if (!this.utilService.mbWrite) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
        return false;
      };
      if (CustomValidator.bmgfNullCheckValidator(this.mobjSmplGrpMasterModel.SmGroupId)) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate("COMMON.MSG.COPY"));
        document.getElementById("txtPrdKey").focus();
        return;
      }
      if (this.mbChange || this.FieldExtender.mbChange == true || this.customCtrl.mbChange == true) {
        this.mbSaveDialogView = true;
        this.mbIsCopyMode = true;
        return false;
      }
      this.bmlfCopyData();
    }
  }
  bmlfOnConfirmModal(psype) {
    if (psype == 'YES') {
      this.bmlfSubmitData();
      this.mbSaveDialogView = false;
    } else if (psype == 'NO') {
      this.mbSaveDialogView = false;
      if (this.mbIsSearchMode) {
        this.mbIsSearchMode = false;
        this.bmlfOpenLookupModal('SmGroupId', this.msScreenName, 'SMGroupMasterList', 'click', '', 'custom', 'cmdSearch', 'SAMPLEGROUP.GROUPLOOKUP');
      } else if (this.mbIsAddMode) {
        this.mbIsAddMode = false;
        this.bmlfRefreshForm();
      } else if (this.mbIsCopyMode) {
        this.mbIsCopyMode = false;
        this.bmlfCopyData();
      } else if (this.mbIsDeleteMode) {
        this.mbIsDeleteMode = false;
        this.mbShowDeleteDialog = true;
      }
      else {
        this.router.navigate(["/main/srm/sample-group"]);
      }
    } else {
      this.mbSaveDialogView = false;
      this.mbIsSearchMode = false;
      this.mbIsCopyMode = false;
      this.mbIsAddMode = false;
      this.mbIsCloseMode = false;
    }
  }
  // Delete Sales Order Dialog events
  bmlfonDialogAction(psAction) {
    if (psAction == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (psAction == 'Yes') {
      if (this.msFormMode == "I") {
        this.bmlfRefreshForm();
        return false;
      }
      let parrHeaderData = [];
      parrHeaderData = [{ SmGroupId: this.mobjSmplGrpMasterModel.SmGroupId }]
      let reqData: any = {
        JSON: JSON.stringify({
          ParaData: parrHeaderData,
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.formService.bmlfDeleteSampleGroupData(reqData).subscribe(res => {
        if (res == null) {
          this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          {
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.DELETE'));
            this.mbShowDeleteDialog = false;
            let deleteKey = this.msScreenName + " — " + this.msPrdKey;
            this.FieldExtender.bmgfDeleteData(deleteKey);
            this.bmlfRefreshForm();
          }
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
          this.mbShowDeleteDialog = false;
        }
      })
    }
  }
  bmlfOpenSelectedRecord(data) {
    let param = { prdKey: data.SmGroupId };
    this.utilService.bmgfRefreshReuse('/main/srm/sample-group/manage', true, param);
  }
  // Refresh Sales Order
  bmlfRefreshForm() {
    this.utilService.bmgfRefreshReuse('/main/srm/sample-group/manage', false);
  }
  // Copy Current Data
  bmlfCopyData() {
    let HeaderData = Object.assign({}, this.mobjSmplGrpMasterModel);
    HeaderData.SmGroupId = "";
    let parrCopyData = {
      HeaderData: HeaderData,
      prdKey: this.mobjSmplGrpMasterModel.SmGroupId
    }
    localStorage.setItem('copySampleGroupData', JSON.stringify(parrCopyData));
    this.bmlfRefreshForm();
  }


  //Open Task MODAL
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = this.mobjSmplGrpMasterModel.SmGroupId;
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }


  /**----E- Signature Code----- */
  // check e signature
  bmlfCheckEsignApplicableOnForm() {
    let pobjReqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          MenuScreenName: this.msScreenName,
        }],
        Response: [{
          ResponseText: '',
          ErrorCode: ''
        }]
      })
    };
    this.userFieldService.bmlfCheckEsignApplicable(pobjReqData).subscribe(res => {
      if (res == null) {
        return false;
      }
      if (res.Response[0].ErrorCode == '') {
        this.mbIsEsignature = res.ESignData[0].Permision;
        this.mbIsEsignADTPermision = res.ESignData[0].ADTPermision;
        this.mobjSignatureData.reasonMinLength = res.ESignData[0].ReasonLength;
        this.mobjSignatureData.reqSignAuth = res.ESignData[0].NoOfReqSign;
      }
    }, err => {
      console.log(err);
    });
  }
  // open e signature modal
  bmlfopenESignatureModal() {
    this.mbSignatureModal = true;
    this.mobjSignatureData.isShow = true;
    this.mobjSignatureData.primaryKey = this.mobjSmplGrpMasterModel.SmGroupId;
  }
  // check e signature
  bmlfChkESignature(pobjData) {
    this.mbSignatureModal = pobjData.isShow;
    if (pobjData.event == 'Save') {
      this.mbIsEsignature = false;
      this.mobjSmplGrpMasterModel.ESG_REASON = pobjData.changeReason;
      this.mobjSmplGrpMasterModel.ESG_APPROVER = pobjData.eSignApprover;
      this.mbChange = true;
      this.bmlfSubmitData();
    } else if (pobjData.event == 'Cancel') {
      this.mobjCommonAlertData = {}
      this.mbCommonAlertDialog = true;
      this.mobjCommonAlertData.message = this.utilService.bmgfonTranslate('ESIGNREASONMSG');
      this.mobjCommonAlertData.event = 'close';
      this.mbChange = true;
    }
  }
  bmlfEsignAuditAlertEvent(event) {
    if (event == 'No') {

    } else {
      this.mbIsEsignAuditDialog = false;
    }
  }

  // For Help PDF 
  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/Sample Group Master.pdf', '_blank');
  }
}
