  bmlfonDeleteDialogAction(action) {
    if (action == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (action == 'Yes') {
      if (this.msFormMode == "I" && (this.mbChange == true || this.customCtrl.mbChange == true || this.FieldExtender.mbChange == true)) { // || this.FieldExtender.mbChange == true
        this.mbShowDeleteDialog = false;
        this.bmlfonClearControl();
        return;
      }
      // else if (this.mbInsert && this.mbChange) {
      //   this.mbShowDeleteDialog = false;
      //   let deleteKey = this.msScreenName + " — " + this.mObjModel.SmTemplateID;
      //   this.FieldExtender.bmgfDeleteData(deleteKey);
      //   this.bmlfonClearControl();
      //   return false;
      // }
      
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            SmTemplateID: this.mObjModel.SmTemplateID,
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.objService.bmlfonDeleteData(reqData).subscribe(res => {
        if (res == null) {
          this.objToastService.notifyError(this.appUtility.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          {
            if (res.Response[0].ResponseText.search("Reference") != -1) {
              this.objToastService.notifyError(this.appUtility.bmgfonTranslate('COMMON.MSG.EXIST_REF'));
              this.mbShowDeleteDialog = false;
              return;
            } else if (res.Response[0].ResponseText.toUpperCase().search('DELETED') != -1) {
              this.mbShowDeleteDialog = false;
              this.objToastService.notifySuccess((this.appUtility.bmgfonTranslate('COMMON.MSG.DELETE')));
              //let deleteKey = this.msScreenName + " — " + this.mObjModel.SmTemplateID;
              let deleteKey = this.msScreenName + " — " + this.msSmTemplateID;
              this.FieldExtender.bmgfDeleteData(deleteKey);
              this.bmlfonClearControl();
            } else {
              this.objToastService.notifyError(res.Response[0].ResponseText);
              this.mbShowDeleteDialog = false;
            };
          }
        } else {
          this.objToastService.notifyError(res.Response[0].ResponseText);
          this.mbShowDeleteDialog = false;
        }
      });
    }
  }

