  bmlfonDeleteGrid1Line() {
    let pSrequestObj: any = {
      HeaderData: [{
        SmTemplateID: this.mObjModel.SmTemplateID,
        RowId: this.mArrGrid[this.miGrid1DeleteLineIndex]["RowId"],
       // SMGroupSeq: this.msPrForDelete
      }],
      Response: [{ ResponseText: '', 'ErrorCode': '' }]
    };
    let reqData: any = {
      JSON: JSON.stringify(pSrequestObj)
    };

    this.objService.bmlfonDeleteLineData(reqData).subscribe(res => {
      if (res == null) { return false; }
      if (!this.isEmpty(res.Response[0].ErrorCode)) {
        this.objToastService.notifyError(res.Response[0].ResponseText);
      } else {
        this.objToastService.notifySuccess(this.appUtility.bmgfonTranslate('COMMON.MSG.LINEDELETE'));
        this.mArrGrid.splice(this.miGrid1DeleteLineIndex, 1);
      }
    });
  }
  //------------------------------------------------------------------------------------------------
  bmlonCompleteDel() {
    if (!this.isEmpty(this.mObjModel.SmTemplateID)) {
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            SmTemplateID: this.mObjModel.SmTemplateID,
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.objService.bmlfonDeleteData(reqData).subscribe(res => {
        if (res == null) { return false; }
        if (!this.isEmpty(res.Response[0].ErrorCode)) {
          this.objToastService.notifyError(res.Response[0].ResponseText);
        }
        else {
          this.objToastService.notifySuccess((this.appUtility.bmgfonTranslate('COMMON.MSG.DELETE')));
          this.bmlfonClearControl();
        }
      });
    }
  }



