  // Add Line Item Grid 1
  bmlfonAddLineToGrid() {
    const pobjDataCopy: any = Object.assign({}, this.mObjDetailModelBlank);
    let piLastIndex;
    piLastIndex = this.mArrGrid.length;
    if (!this.appUtility.mbWrite) {
      this.objToastService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return false;
    };
    if (this.mObjModel.SmTemplateID == undefined || this.mObjModel.SmTemplateID == '') {
      this.objToastService.notifyError((this.appUtility.bmgfonTranslate('SAMPLTEMPLATEMASTR.MESSAGE.ENTERTEMPLATEID')));
      return false;
    };
   

   

    if (piLastIndex > 0) {
      let index = this.mArrGrid.length - 1;
      if(this.mArrGrid[index]['SMGroupSeq']==0){
        this.objToastService.notifyError((this.appUtility.bmgfonTranslate('SAMPLTEMPLATEMASTR.MESSAGE.DONOTADDZEROINGRPSEQ')));
        return false;
      }
      if(this.mArrGrid[index]['SMPropertySeq']==0){
        this.objToastService.notifyError((this.appUtility.bmgfonTranslate('SAMPLTEMPLATEMASTR.MESSAGE.DONOTADDZEROINPRPSEQ')));
        return false;
      }
      if (this.isEmpty(this.mArrGrid[index]['SMGroupSeq']) || this.isEmpty(this.mArrGrid[index]['SmGroupId']) || this.isEmpty(this.mArrGrid[index]['SmPropertyId']) || this.isEmpty(this.mArrGrid[index]['SMPropertySeq'])) {
        this.objToastService.notifyError((this.appUtility.bmgfonTranslate('SAMPLTEMPLATEMASTR.MESSAGE.ADDLINEDETAIL')) + " " + (index+1).toString());
        let eleID = 'txtValue' + (index).toString();
        return;
        //  setTimeout(function () { document.getElementById(eleID).focus(); }, 10);
      }  
      else {
        pobjDataCopy.Flag = "A";
        pobjDataCopy.SmTemplateID = this.mObjModel.SmTemplateID;
        pobjDataCopy.RowId = this.mArrGrid.length + 1;
        pobjDataCopy.SMGroupSeq = 0;
        pobjDataCopy.SMPropertySeq = 0;
        // pobjDataCopy.Recuserid = this.msUserId;
        // pobjDataCopy.CreateUserID = this.msUserId;
        // pobjDataCopy.ModifyUserID = this.msUserId;
        this.mArrGrid.push(pobjDataCopy);
        let eleID = 'txtvalue' + (piLastIndex).toString();
        //   setTimeout(() => { document.getElementById(eleID).focus(); }, 10);
      }
    } 
    else {
      pobjDataCopy.Flag = "A";
      pobjDataCopy.SmTemplateID = this.mObjModel.SmTemplateID;
      pobjDataCopy.RowId = piLastIndex + 1;
      pobjDataCopy.SMGroupSeq=0;
      pobjDataCopy.SMPropertySeq =0;
      // pobjDataCopy.Recuserid = this.msUserId;
      // pobjDataCopy.CreateUserID = this.msUserId;
      // pobjDataCopy.ModifyUserID = this.msUserId;
      this.mArrGrid.push(pobjDataCopy);
      let eleID = 'txtvalue' + (piLastIndex).toString();
      //  setTimeout(() => { document.getElementById(eleID).focus(); }, 10);
    }
  }
