   // So Footer Action finction
   bmlfFooterActionFn(event) {
    if (event == 'searchEvn') {
        if (this.mbChange || this.customCtrl.mbChange == true) {
            this.mbShowDialog = true;
            this.msCallFor = "Search"
            return false;
        }

        this.bmlfOpenLookupModal('SearchSamplateMaster', 'Sampling Template Master', 'SMTemplateList', 'click', '', 'custom', 'cmdSearch', 'SAMPLTEMPLATEMASTR.SAMPLINGTEMPLATEMASTERLOOKUP')
    } 
  }
  //geid column setting json save functinality
SetGridColumns(GridName, parrGridCol) {
  this.appUtility.GetJsonSetting(this.msDefaultScreenName, this.mobjUserData.UserId, GridName, parrGridCol).then((pArrGridChangedCol: any) => {
    if (pArrGridChangedCol != null && pArrGridChangedCol != undefined) {
      parrGridCol = pArrGridChangedCol
    } 
  }, err => {
    console.log(err);     
  });
}
bmlfFieldEvent(type,rowIndex ) {
  this.mbChange = type;
  if (this.mArrGrid[rowIndex].Flag == " " || this.mArrGrid[rowIndex].Flag == "") {
    this.mArrGrid[rowIndex].Flag = "M";
  }
}
